/**
 *   Name: Bizagi Smartphone Render list Extension
 *   Author: Bizagi UI Forms
 *   Comments:
 *   -   This script will redefine the radio render class to adjust to smartphone devices
 */

// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.radio", bizagi.rendering.combo.prototype);
// Extends itself
bizagi.rendering.smartphone.radio.extend("bizagi.rendering.smartphone.radio", bizagi.rendering.radio.prototype);
// Extends itself
bizagi.rendering.smartphone.radio.extend("bizagi.rendering.smartphone.radio", {}, {

  /**
                                                                                     * Template method to implement in each device to customize each render after processed
                                                                                     */
  postRender: function postRender() {
    var $control = this.getControl();
    this.radio = $control.find("input[type=radio]");

    if (this.properties.editable && (this.value == null || this.value === undefined) && this.properties.value != null) {
      this.value = this.properties.value;
    }
  },

  /**
      * Method to render non editable values
      * @returns {*|jQuery}
      */
  renderReadOnly: function renderReadOnly() {
    var template = this.renderFactory.getTemplate("readonly");

    return $.fasttmpl(template, { value: this.getDisplayValue() });
  },

  /**
      * Method to customize each render after processed in read-only mode
      */
  postRenderReadOnly: function postRenderReadOnly() {
    var $control = this.getControl();
    this.radio = $control.find("input");
  },

  /**
      * Sets the value in the rendered control
      * @param value
      */
  setDisplayValue: function setDisplayValue(value) {
    var self = this;
    var $control = self.getControl();

    // Map data from actions
    value = self.mapValue(value);

    // Set internal value
    self.setValue(value, false);

    if (value !== undefined && value.id != null && value.id !== "") {

      if (this.properties.editable) {

        var radioItem = $control.
        find('input[type="radio"][value="' + value.id + '"]');

        radioItem.prop('checked', 'checked');
      } else {
        self.radio.val(this.getDisplayValue());
        self.radio.attr("id", value.id);
      }
    }
  },

  /**
      * Add action handlers
      */
  configureHandlers: function configureHandlers() {
    var self = this;

    this.radio.change(function () {
      var $selected = $(this);

      var newValue = {
        id: $selected.prop('value'),
        value: $.trim($selected.siblings("label").text()) };


      self.setValue(newValue);
    });
  },

  /**
      * Context to apply styles
      * @param context
      * @returns {jQuery|HTMLElement}
      */
  getElementsToStylize: function getElementsToStylize(context) {
    return $("label.bz-radio__label, .bz-input.bz-input--readonly", context);
  },

  /**
      * Cleans current data
      */
  cleanData: function cleanData() {
    var value = { id: "", label: "" };
    this.setValue(value, false);

    this.clearDisplayValue();
  },

  /**
      * Sets the value in the rendered control
      */
  clearDisplayValue: function clearDisplayValue() {
    if (this.properties.editable) {
      this.radio.prop('checked', false);
    } else {
      this.radio.val(this.getDisplayValue());
    }
  },

  /**
      * Returns the selected value in the template
      * @returns {*}
      */
  getSelectedValue: function getSelectedValue() {
    return this.selectedValue;
  } });