/**
 *   Name: Bizagi Smartphone Render Number Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the number render class to adjust to smartphone devices
 */

// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.number", bizagi.rendering.number.prototype);
// Extends itself
bizagi.rendering.smartphone.number.extend("bizagi.rendering.smartphone.number", {}, {

  /**
                                                                                       * Method to customize each render after processed in read-only mode
                                                                                       * @returns {*|jQuery}
                                                                                       */
  renderReadOnly: function renderReadOnly() {
    var self = this;
    var template = self.renderFactory.getTemplate("number");

    return $.fasttmpl(template, {
      value: self.getDisplayValue(),
      editable: self.properties.editable,
      type: "text",
      isDesign: self.getMode() !== "execution" });

  },

  /**
      * Sets the value in the rendered control
      * @param value
      */
  setDisplayValue: function setDisplayValue(value) {
    var self = this;

    // Call base
    self.setValue(value, false);

    // Set value in input
    if (value != null && self.properties.editable) {
      self.numericInput.val(value);

      self.value = self.properties.value = value;

      // Formats the input
      self.executeFormatCurrencyPlugin();
    }
  },

  /**
      * Retype function
      */
  attachRetypeDouble: function attachRetypeDouble() {
    var self = this;
    var element = self.element;

    var input = $("input", element);
    if (!input || input.length === 0) return;

    // Apply blur handler to check value
    input.blur(function () {
      var control = $(this);
      var previousValue = self.previousValue;
      var value = self.value;
      var typeControl = self.parent.properties.type;

      if (typeControl === "grid") {
        if (previousValue != null && value !== previousValue) {
          if ($(this).data("newValue") !== undefined && control.val() !== $(this).data("newValue"))
          $(this).data().newValue = undefined;
        }

        self.retypeDouble(control);
      } else {
        self.retypeDouble(control);
      }
    });
  },

  /**
      * Retype function
      * @param control
      */
  retypeDouble: function retypeDouble(control) {
    var self = this;
    var currentValue = control.val();
    var isVisible = $(control).attr("type") !== "hidden" && $(control).css("display") !== "none" && $(control).css("visibility") !== "hidden";

    if (isVisible && $(this).data("newValue") === undefined) {

      if (!control.data("oldValue") || $(this).data("oldValue") === "") {

        // Check that there is something in the value or if a value has already been set
        if (currentValue === "" || currentValue === control.attr("newValue")) return;

        self.createTooltip(control, currentValue, self.getResource("render-number-retype"));

      } else {
        // Check that there is something in the value
        if (currentValue === "") return;

        if (currentValue !== control.data("oldValue")) {
          self.setValue("");

          self.createTooltip(control, "", self.getResource("render-text-retype-error"));
        } else {
          control.data("newValue", currentValue);
          control.data("oldValue", "");
          try {
            // Destroy tooltips
            control.tooltip("destroy");
          } catch (e) {
          }
        }
      }
    }
  },

  /**
      * Context to apply styles
      * @param context
      * @returns {jQuery|HTMLElement}
      */

  getElementsToStylize: function getElementsToStylize($context) {
    return $("input", $context);
  } });