/**
 *   Name: Bizagi Smartphone Render Date Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the date render class to adjust to smartphone devices
 */

// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.date", bizagi.rendering.date.prototype);
// Extends itself
bizagi.rendering.smartphone.date.extend("bizagi.rendering.smartphone.date", {
  MAX_YEARS_DISPLAY: 100 },
{
  initializeData: function initializeData(data) {
    // Call base
    this._super(data);
  },

  /**
      * Method to render non editable values
      * @returns {*|jQuery}
      */
  renderReadOnly: function renderReadOnly() {
    var self = this;
    var properties = self.properties;
    var dateFormat = properties.dateFormat;
    var ud = bizagi.util.dateFormatter;

    var format = dateFormat.substring(0, dateFormat.search("h|H") != -1 ? dateFormat.search("h|H") - 1 : dateFormat.length);
    var value = ud.formatDate(ud.getDateFromFormat(self.getValue(), format, self.i18n), properties.fullFormat);
    var data = {
      value: self.getMode() !== "execution" ? properties.fullFormat : value,
      editable: properties.editable };


    var template = this.renderFactory.getTemplate("readonly");
    return $.fasttmpl(template, data);
  },

  /**
      * Method to customize each render after processed in read-only mode
      */
  postRenderReadOnly: function postRenderReadOnly() {
    this.input = $("input", this.getDateControl());
  },

  configureHandlers: function configureHandlers() {
    var self = this;
    var dateControl = self.getControl();
    var inputDate = $(".bz-input-icon", dateControl);

    self.configureMobiscollCalendar();
    self.calendarInstance = inputDate.mobiscroll("getInst");

    // Bind event on input date
    inputDate.on("focus", function (e) {
      e.preventDefault();
      e.stopPropagation();

      if (bizagi.util.isCordovaSupported() && bizagi.util.isAndroidDevice()) {
        cordova.plugins.Keyboard.disableScroll(true);
        cordova.plugins.Keyboard.close();
      }
      self.calendarInstance.show();
    });

    inputDate.attr("keyboard", "disable");
  },

  /**
      * Initializes the Mobiscroll Calendar with the indicated configuration
      */
  configureMobiscollCalendar: function configureMobiscollCalendar() {
    var self = this;
    var properties = self.properties;
    var controlElements = properties.showTime ? ["calendar", "time"] : ["calendar"];
    var dateControl = self.getControl();
    var inputDate = $(".bz-input-icon", dateControl);
    var orientation = self.getOrientation();

    inputDate.mobiscroll().calendar({
      mode: "mixed",
      touchUi: true,
      display: "bottom",
      controls: controlElements,
      min: self.getNormalizeDate(new Date(properties.minValue)),
      max: self.getNormalizeDate(new Date(properties.maxValue)),
      defaultValue: self.getDisplayValue(),
      timeFormat: properties.timePickerFormat ? self.getTimeFormat(properties.timePickerFormat) : "",
      dateFormat: properties.datePickerFormat,
      dateWheels: 'dd/MM/yyyy',
      select: "single",
      selectType: "Day",
      rtl: orientation && orientation === "rtl",
      lang: bizagi.util.languages[BIZAGI_LANGUAGE] || bizagi.localization.language || "en",
      buttons: self.properties.showTime ?
      [
      {
        text: self.getResource("render-timeentry-now"),
        handler: function handler() {
          self.setValueFromCalendarButtons(new Date(), function (v) {
            self.getCalendarInstance().setVal(v, false, false, false);
            self.getCalendarInstance().hide();
          });
        } },

      {
        text: self.getResource("render-plugin-search-users-select-link"),
        handler: "set" },

      {
        text: self.getResource("render-plugin-search-users-clear-button-name"),
        handler: function handler(e, inst) {
          inst.clear();
        } }] :


      [
      {
        text: self.getResource("render-timeentry-now"),
        handler: function handler() {
          self.setValueFromCalendarButtons(new Date(), function (v) {
            self.getCalendarInstance().setVal(v, false, false, false);
            self.getCalendarInstance().hide();
          });
        } },

      {
        text: self.getResource("render-plugin-search-users-clear-button-name"),
        handler: function handler(e, inst) {
          inst.clear();
        } }],


      onSet: function onSet() {
        self.onChangeHandler();
      },
      onDayChange: function onDayChange(obj) {
        self.setValueFromCalendarButtons(obj.date, function (v) {
          self.getCalendarInstance().setVal(v, false, false, false);
          if (!self.properties.showTime) self.getCalendarInstance().hide();
        });
      },
      onClear: function onClear() {
        self.setValueFromCalendarButtons(null, function (v) {
          self.getCalendarInstance().setVal(v, false, false, false);
          self.getCalendarInstance().hide();
        });
      },
      onBeforeShow: function onBeforeShow(event, inst) {
        //self.triggerGlobalHandler("closeModalValidationMessage", {});
        self.triggerGlobalHandler("modalViewDidAppear", {});

        inst.option({
          max: bizagi.util.dateFormatter.getDateFromInvariant(properties.maxValue, properties.showTime),
          min: bizagi.util.dateFormatter.getDateFromInvariant(properties.minValue, properties.showTime) });


        //TODO temporal fix in ios smartphone when a notification container is already displayed (QAF-6385 / MOB-171), review mobiscroll.min versions
        if (bizagi.util.isiOSDevice()) {
          $(".ui-bizagi-notifications-container").css("display", "none");
        }
      },
      onClose: function onClose(event, inst) {
        // Your custom event handler goes here
        self.triggerGlobalHandler("modalViewDidDisappear", {});
        $(".mbsc-fr-scroll").css("overflow-y", "auto");
      },
      onShow: function onShow(event, inst) {
        $(".mbsc-fr-scroll").css("overflow-y", "hidden");
      } });

  },

  /**
      * Get the DOM Control
      */
  getDateControl: function getDateControl() {
    return $(".bz-input-icon", this.getControl());
  },

  /**
      * Set the control value when the control change
      */
  onChangeHandler: function onChangeHandler(isNow) {
    var self = this;
    var properties = self.properties;
    var currentDate = self.getNormalizeDate(self.getCalendarInstance().getVal());
    var currentDateString = "";
    var ud = bizagi.util.dateFormatter;

    if (!currentDate || currentDate == 0) {
      return;
    }
    //Attemp set the value of the datepicker control. If not valid go back to the previous.
    var previousValue = self.value;

    //sets the value with the current format date, changed to use the same displayValue method
    if (isNow == true) {
      currentDate = self.getNormalizeDate(new Date());
      currentDateString = ud.formatDate(currentDate, "MM/dd/yyyy HH:mm:ss");
      self.value = currentDateString;
    } else {
      self.value = ud.formatInvariant(currentDate, properties.showTime);
    }

    if (isNow || currentDate) {
      self.value = previousValue;
      currentDateString = ud.formatDate(currentDate, "MM/dd/yyyy HH:mm:ss");
      self.setValue(currentDateString);
      self.setDisplayValue(currentDateString);
    }
  },

  /**
      * Apply the final format that will be showed on the input
      */
  getFullFormatedDate: function getFullFormatedDate(currentDate, currentTime) {
    var fullDate = "";

    if (currentDate.length > 0) {
      fullDate = currentDate;
    } else if (currentDate !== undefined) {
      fullDate = bizagi.util.dateFormatter.formatISO(currentDate, false);
    } else {
      fullDate = bizagi.util.dateFormatter.formatISO(new Date(), false);
    }

    fullDate += currentTime;
    var dateFromFormat = bizagi.util.dateFormatter.getDateFromFormat(fullDate, "yyyy-MM-dd H:m");

    return bizagi.util.dateFormatter.formatDate(dateFromFormat !== 0 ? dateFromFormat : currentDate, "yyyy-MM-dd H:mm:ss");
  },

  /*
      *  Checks if the date could be localized using site language
      */
  preprocessDateValue: function preprocessDateValue(date) {
    return bizagi.util.dateFormatter.formatDate(date, this.properties.fullFormat);
  },

  /**
      * Public method to determine if a value is valid or not
      */
  isValid: function isValid(invalidElements) {
    var self = this;
    var properties = self.properties;
    var bValid = true;
    var value = self.getValue();

    if (value) {
      if (bizagi.util.parseBoolean(properties.editable)) {
        value = self.getNormalizeDate(bizagi.util.dateFormatter.getDateFromInvariant(value));

        if (value != 0) {
          // Check min value and max value
          if (properties.minValue && typeof properties.minValue === "string") {
            bValid = self.checkMinAndMaxValue(value, true, properties, invalidElements) ? false : bValid;
          }

          if (properties.maxValue && typeof properties.maxValue === "string") {
            bValid = self.checkMinAndMaxValue(value, false, properties, invalidElements) ? false : bValid;
          }
        }
      }
    }

    if (bizagi.util.parseBoolean(properties.editable)) {
      // Check required
      if (properties.required && !self.hasValue()) {
        self.showMandatoryMessage(properties, invalidElements);
      }
    }

    if (bValid) {
      // Clear error message
      self.setValidationMessage("");
    }

    return bValid;
  },

  /**
      * To validate the min and max values
      * @param {} value
      * @param {} isMinValue
      * @param {} properties
      * @param {} invalidElements
      */
  checkMinAndMaxValue: function checkMinAndMaxValue(value, isMinValue, properties, invalidElements) {
    var vValue = this.getNormalizeDate(
    bizagi.util.dateFormatter.getDateFromInvariant(isMinValue ? properties.minValue : properties.maxValue));


    //In case the value from the minValue or maxValue are assigned from a Date control that does not have the option  "showTime!
    if (vValue == 0) {
      var resultValue = "";
      if (properties.timeFormat.indexOf(":ss") == -1) {
        if (isMinValue) {
          resultValue = properties.minValue + ":00";
        } else {
          resultValue = properties.maxValue + ":00";
        }
      }
      vValue = this.getNormalizeDate(bizagi.util.dateFormatter.getDateFromFormat(resultValue, properties.fullFormat + ":ss"));
    }

    if (isMinValue && value.getTime() < vValue.getTime() || !isMinValue && value.getTime() > vValue.getTime()) {
      var formatedDate = bizagi.util.dateFormatter.formatDate(vValue, properties.fullFormat);
      var message = "";

      if (isMinValue) {
        message = this.getResource("render-date-minimum-validation").
        replaceAll("#label#", properties.displayName).
        replaceAll("#minValue#", formatedDate);
      } else {
        message = this.getResource("render-date-maximum-validation").
        replaceAll("#label#", properties.displayName).
        replaceAll("#maxValue#", formatedDate);
      }

      invalidElements.push({ xpath: properties.xpath, message: message });
      return true;
    }
    return false;
  },

  /**
      * To show the mandatory values message
      * */
  showMandatoryMessage: function showMandatoryMessage(properties, invalidElements) {
    var self = this;
    var message = self.getResource("render-required-text").replaceAll("#label#", properties.displayName);

    invalidElements.push({ xpath: properties.xpath, message: message });

    return false;
  },

  setHour: function setHour(date) {
    return date.replace(/\s(\d:)/, " 0$1");
  },

  /**
      * Cleans the control fields as it requires
      */
  clearDisplayValue: function clearDisplayValue() {
    var self = this;

    self.date = undefined;
    self.time = undefined;
    self.timeS = undefined;
    self.setValue("");
    self.setDisplayValue("");
  },

  isValidMinMaxValue: function isValidMinMaxValue() {
    var self = this;
    var isValid = true;
    var value = self.getValue();

    if (value) {
      value = bizagi.util.dateFormatter.getDateFromInvariant(value);

      if (value !== 0) {
        // Check min value and max value
        if (self.properties.minValue && typeof self.properties.minValue === "string") isValid = self.validateMinAndMaxValue(value, true);

        if (self.properties.maxValue && typeof self.properties.maxValue === "string") isValid = self.validateMinAndMaxValue(value, false);
      }
    }

    return isValid;
  },

  validateMinAndMaxValue: function validateMinAndMaxValue(currentDate, isMinValue) {
    var self = this;
    var date = isMinValue ? self.properties.minValue : self.properties.maxValue;
    var formattedDate = self.getNormalizeDate(bizagi.util.dateFormatter.getDateFromInvariant(date));

    return !(
    isMinValue && currentDate.getTime() < formattedDate.getTime() ||
    !isMinValue && currentDate.getTime() > formattedDate.getTime());

  },

  getElementsToStylize: function getElementsToStylize($context) {
    return $("input, .bz-input--readonly", $context);
  },

  /**
      * Gets Mobiscroll Calendar's instance
      * @returns {Object} Mobiscroll's instance
      */
  getCalendarInstance: function getCalendarInstance() {
    return this.calendarInstance || (this.calendarInstance = $(this.getDateControl()).mobiscroll("getInst"));
  },

  setDisplayValue: function setDisplayValue(value) {
    var self = this;
    if (self.getMode() !== "execution") {
      return;
    }

    var properties = self.properties;
    var dateControl = self.getControl();
    var input = $(".bz-input-icon__input, .bz-input--readonly", dateControl);
    var ud = bizagi.util.dateFormatter;

    if (bizagi.util.isNumeric(value) && value !== 0) {
      value = ud.formatInvariant(new Date(value), properties.showTime);
    }

    if (value) {
      if (typeof value == "string") {
        value = self.getNormalizeDate(ud.getDateFromInvariant(value, properties.showTime));
      } else {
        value = self.getNormalizeDate(value);
      }
    }

    // Assert that the date could be parsed
    var ci = self.getCalendarInstance();
    if (properties.editable && ci.setVal) ci.setVal(value);
    self.setValue(ud.formatInvariant(value, true));
    properties.displayValue = ud.formatDate(value, properties.fullFormat, self.i18n);

    // Set display value
    if (typeof properties.displayValue === "string" || typeof properties.displayValue === "number") {
      input.prop("value", properties.displayValue);
      input.prop("title", properties.displayValue);
    }

    if (!properties.editable) {
      input.html(properties.displayValue);
    }
  },
  /**
      * Sets value to the control and execute a callback function
      * @param {Date} date date to asign
      * @param {Function} callBack function to execute
      * @returns {}
      */
  setValueFromCalendarButtons: function setValueFromCalendarButtons(date, callBack) {
    this._super(date);
    if (typeof callBack === "function") callBack(date);
    this.onChangeHandler();
  } });