/**
 *   Name: Bizagi smartphone Render Document Generator Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the document generator render class to adjust to smartphone devices
 */

// Extends from render smartphone base and document base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.document", bizagi.rendering.document.prototype);
// Extends itself
bizagi.rendering.smartphone.document.extend("bizagi.rendering.smartphone.document", {
  ACTION_SHOW: "show",
  ACTION_DOWNLOAD: "download",
  ACTION_DELETE: "delete" },
{

  /**
   * This method tries to get the HTML template an render the current object inside
   * @return {jQuery} $.Deferred()
   */
  renderControl: function renderControl() {
    var self = this;
    var template = this.renderFactory.getTemplate(self.getTemplate());
    var documents = this.properties.value || [];

    var data = {
      downloadalldocuments: this.properties.downloadalldocuments,
      allowgenerate: this.properties.allowGenerate,
      noFiles: documents ? documents.length === 0 : true,
      append: this.properties.append,
      caption: this.properties.caption };


    // Render template
    var html = $.fasttmpl(template, data);

    // Render current children
    var items = this.renderDocumentsHtml(documents);
    html = this.replaceFilesHtml(html, items);

    // Render current children
    var actions = this.renderActionsHtml(documents, data.noFiles, data.downloadalldocuments);
    html = this.replaceActionsHtml(html, actions);

    return html;
  },

  /**
      * Method to customize each render after processed
      */
  postRender: function postRender() {
    var $control = this.getControl();

    this.$inputDocument = $(".bz-document-template__container", $control);
    this.$listDocuments = $("[data-component=files]", $control);
    this.$listActions = $("[data-component=actions]", $control);

    this._super();
  },

  /**
      * Method to render non editable values
      * @returns {*|jQuery}
      */
  renderReadOnly: function renderReadOnly() {
    var self = this;
    var template = this.renderFactory.getTemplate(self.getTemplateReadonly());
    var documents = this.properties.value || [];
    var noFiles = documents ? documents.length === 0 : true;

    // Render template
    var html = $.fasttmpl(template, { noFiles: noFiles });

    // Render current children
    var items = this.renderDocumentsHtml(documents);
    html = this.replaceFilesHtml(html, items);

    return html;
  },

  /**
      * Method to customize each render after processed in read-only mode
      */
  postRenderReadOnly: function postRenderReadOnly() {
    var $control = this.getControl();

    this.$listDocuments = $("[data-component=files]", $control);
  },

  /**
      * TMethod to customize the render's behaviour to add handlers
      */
  configureHandlers: function configureHandlers() {
    var self = this;

    // Attach handler
    this.$inputDocument.on("click", function (evt) {
      self.properties.buttonExecuted = true;

      if (self.properties.validate) {
        // Check if the form is valid, then generate
        if (self.validateContainer()) self.documentTemplateGenerate();
      } else {
        // Just generate the form
        self.documentTemplateGenerate();
      }

      self.properties.buttonExecuted = false;
    });

    // Configure delete and preview handler
    this.$listDocuments.on("click", "[data-action]", function (evt) {
      var $context = $(this);

      var item = $context.parent(".bz-document");
      var documentFile = item.data();
      var action = $context.data("action");

      self.processAction(action, documentFile);
    });

    // Configure delete and download handler
    self.$listActions.on("click", "[data-action]", function (evt) {
      var $context = $(this);

      var item = $context.parent(".bz-document__download");
      var documentFile = item.data();
      var action = $context.data("action");

      self.processAction(action, documentFile);
    });
  },

  /**
      * Method to customize the render's behaviour to add handlers when the control is readonly
      */
  configureHandlersReadOnly: function configureHandlersReadOnly() {
    var self = this;

    // Configure delete and preview handler
    this.$listDocuments.on("click", "[data-action]", function (evt) {
      var $context = $(this);

      var item = $context.parent(".bz-document");
      var documentFile = item.data();
      var action = $context.data("action");

      self.processAction(action, documentFile);
    });
  },

  /**
      * This method must be implemented by the specific class of each device. (Virtual method)
      * @param file
      * @returns {*|*|*}
      */
  renderAllDocumentDownload: function renderAllDocumentDownload(file) {
    var self = this;
    var template = self.renderFactory.getTemplate(self.getTemplateDownload());
    var url = self.buildAllDocumentsUrl();

    // Don't render urls when not running in execution mode
    if (!this.isExecutionMode()) url = "javascript:void(0);";

    var html = $.fasttmpl(template, {
      url: url,
      filename: file.fileName,
      joined: true });


    return html;
  },

  /**
      * Redraw documents when needed
      * @param documents
      * @param noFiles
      * @param downloadAllDocuments
      */
  redrawDocuments: function redrawDocuments(documents, noFiles, downloadAllDocuments) {
    var self = this;
    var $control = self.getControl();
    var $documentContainer = $("[data-component='files']", $control);
    var $actionContainer = $("[data-component='actions']", $control);

    // Clear current links
    $documentContainer.empty();
    $actionContainer.empty();

    // Draw links again
    var items = this.renderDocumentsHtml(documents);
    $documentContainer.append(items);

    // Check if there are documents and the flag to download all are available to show the link.
    var actions = this.renderActionsHtml(documents, noFiles, downloadAllDocuments);
    $actionContainer.append(actions);

    //Set value
    self.setValue(documents);
  },

  /**
      * Method to customize each render after processed (Virtual method)
      * @param url
      * @param filename
      */
  showDocumentPreview: function showDocumentPreview(url, filename) {
    var self = this;
    bizagi.loading.start();

    $.when(bizagi.util.media.downloadFile(url, filename)).
    fail(function (error) {
      if (error.responseText) {
        self.getFormContainer().failHandler(error.responseText);
      }
    }).always(function () {
      bizagi.loading.stop();
    });
  },

  /**
      * Process request of DocumentTemplateGenerate action
      * @param documents
      * @param noFiles
      * @param downloadAllDocuments
      */
  resolveDocumentTemplateGenerate: function resolveDocumentTemplateGenerate(documents, noFiles, downloadAllDocuments) {
    // Redraw documents
    this.redrawDocuments(documents, noFiles, downloadAllDocuments);
    this.value = documents;
  },

  /**
      * Process request of DocumentTemplateGenerate action
      * @param documents
      * @param noFiles
      * @param downloadAllDocuments
      */
  resolveDeleteDocumentTemplate: function resolveDeleteDocumentTemplate(documents, noFiles, downloadAllDocuments) {
    // Redraw documents
    this.redrawDocuments(documents, noFiles, downloadAllDocuments);
    this.value = documents;
  },

  processAction: function processAction(action, documentFile) {
    if (action === this.Class.ACTION_DELETE) {
      this.deleteDocumentTemplate(documentFile.filename);
    } else if (action === this.Class.ACTION_SHOW) {
      this.showDocumentPreview(documentFile.url, documentFile.filename);
    } else if (action === this.Class.ACTION_DOWNLOAD) {
      this.showDocumentPreview(documentFile.url, documentFile.filename);
    }
  } });