/**
 *   Name: Bizagi Render HandSignature Class
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the Hand Signature render class to adjust to smartphone devices
 */

// Extends from render Smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.handSignature", {
  // Statics
  BA_ACTION_PARAMETER_PREFIX: bizagi.render.services.service.BA_ACTION_PARAMETER_PREFIX,
  BA_CONTEXT_PARAMETER_PREFIX: bizagi.render.services.service.BA_CONTEXT_PARAMETER_PREFIX,
  BA_PAGE_CACHE: bizagi.render.services.service.BA_PAGE_CACHE },
bizagi.rendering.handSignature.prototype);
// Extends itself
bizagi.rendering.smartphone.handSignature.extend("bizagi.rendering.smartphone.handSignature", {}, {

  /**
                                                                                                     * Whether or not is a native device
                                                                                                     * @var isNative boolean
                                                                                                     */
  isNative: false,

  /**
                    * Whether or not is a offline control
                    * @var isOfflineForm boolean
                    */
  isOfflineForm: false,

  /**
                         * Update or init the element data
                         * @param {} data
                         */
  initializeData: function initializeData(data) {
    // Call base
    this._super(data);

    // Check if offline forms is supported
    this.isOfflineForm = bizagi.util.isOfflineForm({ context: this });
    this.isNative = bizagi.util.isCordovaSupported();
  },

  /**
      * Add action handlers
      */
  configureHandlers: function configureHandlers() {
    var self = this;
    var $actions = $(".bz-actions", this.getControl());
    var $container = self.getSignatureContainer();

    var autoSave = bizagi.util.debounce2(function (params) {
      if (!changed) return;

      var data = Array.prototype.slice.call(params || []);
      var dataImage = (data && data[0] || []).dataImage || "";

      self.saveDeferred = $.when(self.saveSignature(dataImage));
    }, !self.isOfflineForm ? self.DELAY_TO_SAVE : 0);

    var changed = false;

    $container.bind("change", function () {
      changed = true;
      if ($(this).BzjSignature("isModified")) {
        self.saveTimestamp = new Date().getTime();
        var $canvas = $(".bz-signature__container-canvas", this)[0];
        var dataImage = $canvas.toDataURL("image/png");
        autoSave.fire({ dataImage: dataImage });
        self.setData(dataImage.replace(/^data:image\/(png|jpg|jpeg);base64,/, ""));
        self.properties.url = dataImage;
        self.setState(self.STATE_TYPE.done);
      }
    });

    $actions.on("click", "[data-action='clear']", function () {
      changed = true;
      autoSave.cancel();
      self.onSignatureClear($container);
    });

    $actions.on("click", "[data-action='edit']", function () {
      self.onSignatureEdit($container);
      changed = false;
    });

    $actions.on("click", "[data-action='save']", function () {
      if (!changed) {
        self.setState(self.STATE_TYPE.edit);
        $container.BzjSignature("disable");
        return;
      }

      self.onSignatureSave($container, autoSave);
      changed = false;
    });

  },

  onSignatureClear: function onSignatureClear($container) {
    var self = this;

    $container.BzjSignature("reset");

    if (self.hasValue()) {
      bizagi.loading.start();
      self.setData(self.EMPTY_VALUE);
      self.properties.url = self.EMPTY_VALUE;
      $.when(self.deleteSignature()).then(function (result) {
        self.setData(result);
        self.properties.url = result;
      }).fail(function (error) {
        self.executeFormFailHandler(error);
      }).always(function () {
        bizagi.loading.stop();
      });
    }
  },

  onSignatureEdit: function onSignatureEdit($container) {
    this.setState(this.STATE_TYPE.draw);
    $container.BzjSignature("enable");
  },

  onSignatureSave: function onSignatureSave($container, autoSave) {
    var self = this;

    $container.BzjSignature("disable");

    if ($container && $container.BzjSignature("isModified")) {
      var $canvas = $(".bz-signature__container-canvas", $container)[0];
      bizagi.loading.start();

      autoSave.cancel();
      self.saveSignature($canvas.toDataURL("image/png")).always(function () {
        self.setState(self.STATE_TYPE.edit);
        bizagi.loading.stop();
      });
    } else {
      self.setState(self.STATE_TYPE.edit);
    }
  },

  /**
      * Apply plugin
      */
  renderSignature: function renderSignature() {
    var self = this;
    var $container = self.getSignatureContainer();
    var config = {
      sizeRatio: 3,
      cssclass: "bz-signature__container-canvas",
      color: "#000" };


    $container.empty();
    $container.BzjSignature(config);
    $container.BzjSignature("disable");

    if (self.pluginInitialized && self.properties.url) {
      self.setSignature($container);
    } else {
      self.renderSignatureValue($container);
    }

    // Only apply for mobile
    var $canvas = $(".bz-signature__container-canvas", $container);
    var height = Math.round($canvas.width() / 2);

    $canvas.attr("height", height).css("height", height);
    $canvas.css("width", "100%");

    // Required for resize observer
    self.pluginInitialized = true;
  },

  /**
      * Set data to control
      * @param $container
      * @returns {*}
      */
  renderSignatureValue: function renderSignatureValue($container) {
    var self = this;

    bizagi.util.sleepUntil(function () {
      return !self.dataService.getFileService().isLoading(self);
    }).then(function () {
      return self.buildItemUrl();
    }).then(function (dataUrl) {
      if (!self.isDisposed()) {
        // Fake empty
        if (dataUrl === self.BASE_IMAGE + self.EMPTY_IMAGE) {
          dataUrl = "";
        }

        var url = dataUrl !== "" ? dataUrl : "";
        self.properties.url = url;

        if (!bizagi.util.isEmpty(url)) {
          $container.BzjSignature("setData", url);
        } else {
          self.setData(self.EMPTY_VALUE);
        }
      }
    });
  },

  /**
      * Execute upload image
      * @param dataImage
      * @returns {*}
      */
  postSignature: function postSignature(dataImage) {
    var signature = dataImage.replace(/^data:image\/(png|jpg|jpeg);base64,/, "");

    if (!this.isOfflineForm) {
      return this.postSignatureOnline(signature);
    }

    return this.postSignatureOffline(signature);
  },

  /**
      * Execute upload image for offline mode
      * @param signature
      * @returns {*}
      */
  postSignatureOffline: function postSignatureOffline(signature) {
    var self = this;

    return self.saveImageOffline(signature).then(function () {
      self.properties.isChanged = true;
      return signature;
    });
  },

  /**
      * Delete data image
      * @returns {*}
      */
  deleteSignature: function deleteSignature() {
    var self = this;

    if (!self.isOfflineForm) {
      return this.deleteImage().then(function () {
        return self.EMPTY_VALUE;
      });
    }
    return self.saveImageOffline(self.EMPTY_IMAGE).then(function () {
      self.properties.isChanged = true;
      return self.EMPTY_VALUE;
    });
  },

  /**
      * Builds the upload item url
      * @param params
      * @returns {*}
      */
  buildItemUrl: function buildItemUrl(params) {
    if (this.isOfflineForm) {
      return this.buildItemUrlOffline(params);
    } else {
      return this._super(params);
    }
  } });