/*
*   Name: BizAgi smartphone Render Association
*   Author: UI Forms Team
*   Comments: Defines the association control
*/

// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.association", bizagi.rendering.association.prototype);
// Extends itself
bizagi.rendering.smartphone.association.extend("bizagi.rendering.smartphone.association", {}, {

  /**
                                                                                                  * Template method to implement in each children to customize each control
                                                                                                  * 
                                                                                                  * @returns {} 
                                                                                                  */
  renderControl: function renderControl() {
    var self = this;
    var template = self.renderFactory.getTemplate("association");
    var def = new $.Deferred();

    self.properties.property = "flipped";
    self.properties.idRender = self.properties.id;

    // Define flipped
    $.when(self.getFlipAssociation()).done(function (flip) {
      // Make json base, create self.properties.formatJson
      self.makeJsonBase();

      self.properties.flipped = bizagi.util.parseBoolean(flip) || false;

      var columnData = self.getColumnData();

      var html = $.fasttmpl(template, $.extend(self.properties, { columnData: columnData }), {
        getColumnData: self.getColumnData,
        getRightAssociation: self.getRightAssociation });


      /* by default need set self.properties.formatJson */
      self.setValue(JSON.encode(self.properties.formatJson));

      def.resolve(html);
    });
    return def.promise();
  },


  /**
      * Get Left Data, verify if flipper flags sets true
      *
      * @param column {'0'||'1'}
      */
  getColumnData: function getColumnData() {
    var self = this;
    var properties = self.properties;
    var data = [];

    self.data = properties;

    var dataAssociation = properties.flipped ? properties.rightData : properties.leftData;

    $.each(dataAssociation, function (key, item) {
      data.push({
        id: item.id,
        value: item.value,
        association: self.getRightAssociation(item.id) });

    });

    return data;
  },

  postRender: function postRender() {
    var self = this;
    var isExecution = self.getMode() === "execution";
    var $control = self.getControl();

    // Cache control
    self.association = $(".bz-association", $control);

    // Add Events at Association Control
    if (isExecution) {self.addEventControl();}

    self.showDefaultGroupList();
  },

  showDefaultGroupList: function showDefaultGroupList() {
    var self = this;
    var $control = self.getControl();
    var $groups = $('.bz-association-group', $control);

    if ($groups.length > 0) {
      $("[data-action='collapse']", $groups[0]).toggleClass("bz-icn--expanded");

      $(".bz-association-group__list", $groups[0]).
      slideDown(300, function () {
        $(this).toggleClass("bz-association-group__list--hidden");
      });
    }
  },

  /**
      * Add event to control
      * @returns {} 
      */
  addEventControl: function addEventControl() {
    var self = this;
    var template = self.renderFactory.getTemplate("association");
    var control = self.getControl();

    // Bind check toggle
    self.association.
    on("change", "input:checkbox", function () {
      var $input = $(this);
      var parentId = $input.data("parent-id");
      var id = $input.data("id");

      if ($input.prop("checked")) {self.addElement(parentId, id);} else
      {self.removeElement(parentId, id);}
    });

    // Bind flip actions
    self.association.
    find("[data-action='flip']").
    click(function () {
      $(control).empty();

      // Define flipped
      self.properties.flipped = !self.properties.flipped;

      // Send flip data to server
      self.properties.flipstate = self.properties.flipped;

      self.dataService.getFlipAssociation(self.properties);

      var columnData = self.getColumnData();

      // Render template
      $($.fasttmpl(template, $.extend(self.properties, { columnData: columnData }), {
        getColumnData: self.getColumnData,
        getRightAssociation: self.getRightAssociation })).
      appendTo(control);

      self.postRender();
    });

    //header
    self.association.on("click", "[data-action='collapse']", function () {
      var $element = $(this);
      var $groupList =
      $element.
      parents('.bz-association-group').
      find(".bz-association-group__list");

      self.closeAccordionSection();
      if ($groupList.hasClass("bz-association-group__list--hidden")) {

        $element.toggleClass("bz-icn--expanded");

        $groupList.slideDown(300, function () {
          $(this).toggleClass("bz-association-group__list--hidden");
        });
      }
    });
  },

  closeAccordionSection: function closeAccordionSection(active) {
    var self = this;
    var $control = self.getControl();

    $("[data-action='collapse']", $control).removeClass('bz-icn--expanded');

    $(".bz-association-group__list", $control).
    slideUp(300, function () {
      $(this).addClass("bz-association-group__list--hidden");
    });
  },

  renderReadOnly: function renderReadOnly() {
    var self = this;

    return self.renderControl();
  },

  /**
      * Render for readonly
      * @returns {} 
      */
  postRenderReadOnly: function postRenderReadOnly() {
    this.postRender();
  },

  /* SET-DISPLAY-VALUE
      =====================================================*/
  setDisplayValue: function setDisplayValue(value) {
    // Set internal value
    this.setValue(value, false);
  },

  getLabel: function getLabel() {
    if (!this.label) {
      var label = $("> .bz-form-label", this.element || this.observableElement);

      if (label.length > 0)
      this.label = label;

      return label;
    }
    return this.label;
  } });