/*
*   Name: Bizagi Smartphone ECM Column Decorator Extension
*   Author: UI Forms Team
*   Comments:
*   -   This script will redefine the ECM column decorator class to adjust to smartphone devices
*/

// Extends from column
bizagi.rendering.smartphone.columns.column.extend("bizagi.rendering.smartphone.columns.uploadecm", {}, {

  /**
                                                                                                          * Apply custom overrides to each decorated instance
                                                                                                          * @param {} decorated      
                                                                                                          */
  applyOverrides: function applyOverrides(decorated) {
    this._super(decorated);

    if (decorated && decorated.getControl) {
      this.originalGetControl = decorated.getControl;
      decorated.getControl = this.getControl;
    }

    decorated.getUploadXpath = this.getUploadXpath;
    decorated.getXpathContext = this.getECMXpathContext;

    decorated.buildItemUrl = function (file) {
      var self = this;
      var properties = self.properties;
      var form = self.getFormContainer();
      var columnXpath = self.getUploadXpath(self.surrogateKey);

      columnXpath = columnXpath.split(".");

      var nameFile = columnXpath[columnXpath.length - 1];
      var xpathContext = self.getXpathContext(self.surrogateKey);

      return self.dataService.getUploadFileUrl({
        idRender: properties.id,
        xpath: nameFile,
        xpathContext: xpathContext,
        idPageCache: properties.idPageCache,
        fileId: file.idFileUpload,
        sessionId: form.properties.sessionId,
        contexttype: properties.contexttype });

    };

    decorated.buildAddParams = function () {
      var self = this;
      var properties = self.properties;
      var form = self.getFormContainer();
      var columnXpath = self.getUploadXpath(self.surrogateKey);

      columnXpath = columnXpath.split(".");
      var nameFile = columnXpath[columnXpath.length - 1];
      var xpathContext = self.getXpathContext(self.surrogateKey);

      var data = {
        url: properties.addUrl,
        xpath: properties.xpath,
        idAttrib: properties.idAttrib,
        xpathContext: xpathContext,
        metaValues: [],
        idSession: form.properties.sessionId,
        contexttype: "entity",
        validExtensions: properties.validExtensions,
        maxSize: properties.maxSize };


      data[self.Class.BA_CONTEXT_PARAMETER_PREFIX + self.Class.BA_PAGE_CACHE] = properties.idPageCache;

      return data;
    };
  },

  /**
      * Post process the element after it has been rendered
      * @param {} surrogateKey      
      */
  getUploadXpath: function getUploadXpath(surrogateKey) {
    surrogateKey = this.surrogateKey || surrogateKey || "";
    return this.grid.properties.xpath + "[id=" + surrogateKey + "]." + this.properties.xpath;
  },


  getControl: function getControl() {
    var result = this._super();

    if (!result || result.length == 0) {
      return $(".ui-bizagi-cell-readonly", this.element || this.observableElement);
    } else {
      return $(".bz-control", this.element || this.observableElement);
    }

  },

  getECMXpathContext: function getECMXpathContext() {
    var xpath = this.grid.properties.xpath + "[id=" + this.surrogateKey + "]";

    return this.column.properties.xpathContext ? this.column.properties.xpathContext + "." + xpath : xpath;
  } });