/**
 *   Name: Bizagi Smartphone Render Button Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the button render class to adjust to smartphone devices
 */

// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.button", bizagi.rendering.button.prototype);
// Extends itself
bizagi.rendering.smartphone.button.extend("bizagi.rendering.smartphone.button", {}, {

  /**
                                                                                       * Whether or not we are offline 'Just a shortcut
                                                                                       * @var isOfflineForm boolean
                                                                                       */
  isOfflineForm: false,

  /**
                         * Update or init the element data
                         * @param {} data
                         */
  initializeData: function initializeData(data) {
    // Call base
    this._super(data);

    // Check if the form is offline
    this.isOfflineForm = bizagi.util.isOfflineForm({ context: this });
  },

  /**
      * Template method to implement in each device to customize the render's behaviour to add handlers
      * @returns {}
      */
  configureHandlers: function configureHandlers() {
    var self = this;

    $(":button", self.getControl()).on("click", function () {
      self.processButton();
    });
  },

  /**
      * In offline forms the button only works for actions and validations
      * @returns {boolean|*}
      */
  executeButton: function executeButton() {
    if (!this.isOfflineForm) {
      return this._super();
    } else {
      // returns false to prevent form refreshing
      return false;
    }
  },

  /**
      * Process the button actions workflow
      * @returns {}
      */
  processButton: function processButton() {
    var self = this;

    if (self.properties.editable) {

      // Check if action need conformation
      if (self.properties.needsUserConfirmation) {
        $.when(bizagi.showConfirmationBox(self.properties.userConfirmationMessage, "Bizagi", "warning")).
        done(function () {
          if (!self.properties.preventDefault)
          self.runButtonAction();
          // Trigger change
          self.triggerRenderChange({ pressed: true });
        });
      } else {
        if (!self.properties.preventDefault)
        self.runButtonAction();

        // Trigger change
        self.triggerRenderChange({ pressed: true });
      }
    }
  },

  getElementsToStylize: function getElementsToStylize($context) {
    return $("button", $context);
  },

  /**
      * Customizes the font weight
      * @param {} $label
      * @param {} bold
      * @returns {}
      */
  changeFontBold: function changeFontBold($label, bold) {
    if (bizagi.util.parseBoolean(bold)) {
      $label.css("font-weight", "bold");
    }

    return this;
  } });