/**
 *   Name: Bizagi Smartphone Search Form Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the container class to adjust to smartphone devices
 */

// Extends from container smartphone base
bizagi.rendering.smartphone.form.extend("bizagi.rendering.smartphone.searchForm", bizagi.rendering.searchForm.prototype);
// Extends itself
bizagi.rendering.smartphone.searchForm.extend("bizagi.rendering.smartphone.searchForm", {}, {

  /*
                                                                                              *   Render the results of the query
                                                                                              *   Resolved in each device
                                                                                              */
  renderResult: function renderResult() {
    var self = this;
    var properties = self.properties;

    // Creates grid definition
    var data = {};
    data.properties = {
      displayName: self.getResource("render-search-advanced-results-label"),
      type: "grid",
      xpath: "searchForm" + properties.id,
      id: properties.id,
      skipInitialLoad: true,
      rowsPerPage: 10,
      sortBy: properties.sortBy,
      sortOrder: properties.sortOrder,
      overrideGetRemoteData: function overrideGetRemoteData(params) {
        return self.getRemoteData(params);
      } };

    data.elements = [];

    // Creates columns
    var columns = properties.columns;
    $.each(columns, function (i) {
      var column = {
        render: {} };

      column.render.properties = {
        displayName: columns[i].caption,
        xpath: columns[i].xpath,
        editable: false };


      $.extend(column.render.properties, self.parseAdditionalProperties(columns[i].dataType));

      // Next conditional added to print only columns and not labels
      // if type = label then column is not rendered on the results table
      if (columns[i].dataType == 0)
      column.render.properties.columnVisible = false;

      // if column is number or monetary push allowDecimals property
      if (columns[i].dataType == 7 || columns[i].dataType == 8)
      column.render.properties.allowDecimals = columns[i].allowDecimals;

      // if column is real or float set allowDecimals property to true
      if (columns[i].dataType == 10 || columns[i].dataType == 11)
      column.render.properties.allowDecimals = true;

      // if column is date push showtime property
      if (columns[i].dataType == 12)
      column.render.properties.showTime = columns[i].showTime;

      data.elements.push(column);
    });

    var grid = self.renderFactory.getRender({
      type: "grid",
      data: data,
      parent: self,
      mode: self.getMode() });


    grid.properties.allowAdd = false;
    grid.properties.allowEdit = false;
    grid.properties.allowDelete = false;

    // Saves reference
    self.resultsGrid = grid;

    return grid.render();
  },

  /*
     *   Template method to implement in each device to customize each container after processed
     */
  postRenderContainer: function postRenderContainer(container) {
    var self = this;
    self._super(container);

    var results = container.find(".bz-container__search-results");
    var mode = self.getMode();

    // Process children elements
    self.container = container;

    // Process result
    var grid = $(results.children()[0]);
    self.resultsGrid.postRenderElement(grid);

    // Apply handlers
    if (mode == "execution") {
      self.configureHandlers();
    }

    // Configure view
    if (mode == "design") {
      self.configureDesignView();
    }

    // Configure view
    if (mode == "layout") {
      self.configureDesignView();
      self.configureLayoutView();
    }
  },

  configureHandlers: function configureHandlers() {
    var self = this;

    // Saves reference
    self.criteriaContainer = $(".bz-container__search-criteria", self.container);
    self.resultsContainer = $(".bz-container__search-results", self.container);

    // Bind to grid row select event
    self.resultsGrid.bind("rowSelected", function (e, ui) {
      if (self.properties.searchParams.hasOwnProperty("multiSelect")) {
        self.trigger("multiSelect", ui);
      } else {
        // Trigger event to bubble up to main container
        self.trigger("instanceSelected", ui);
      }
    });
  },

  /*
     *   Refresh the result panel, making it to query data with current criteria
     */
  refreshResults: function refreshResults() {
    var self = this;
    self.trigger("instancerefresh");

    $.when(self.resultsGrid.refresh()).done(function () {
      self.trigger("instancerefreshdone");

      if (self.resultsGrid.properties.data && self.resultsGrid.properties.data.records == 0 && self.resultsGrid.properties.data.rows.length == 0) {
        var message = bizagi.localization.getResource("workportal-general-no-records-found");
        var title = bizagi.localization.getResource("render-dialog-sendemail-message");

        bizagi.showMessageBox(message, title);
      }
    });
  },
  /*
      *   Publish a global event that the facade will replicate, also will be triggered by the principal form
      */
  triggerGlobalHandler: function triggerGlobalHandler(eventType, data) {
    var self = this;
    self._super(eventType, data);
    if (!self.parent) {
      if (self.observableElement) {
        return self.observableElement.trigger(eventType, data);
      } else {
        return null;
      }
    } else {
      return self.parent.triggerGlobalHandler(eventType, data);
    }
  } });