/**
 *   Name: Bizagi Smartphone Render ECM Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the ecm render class to adjust to smartphone devices
 */

// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.ecm", {
  // Statics
  BA_ACTION_PARAMETER_PREFIX: bizagi.render.services.service.BA_ACTION_PARAMETER_PREFIX,
  BA_CONTEXT_PARAMETER_PREFIX: bizagi.render.services.service.BA_CONTEXT_PARAMETER_PREFIX,
  BA_PAGE_CACHE: bizagi.render.services.service.BA_PAGE_CACHE },
bizagi.rendering.ecm.prototype);

// Extends itself
bizagi.rendering.smartphone.ecm.extend("bizagi.rendering.smartphone.ecm", {
  QUALITY_PICTURE: 50,
  LIMIT: 1, // The maximum number of media in the device user can record in a single capture operation.
  EXTENSIONSIMG: ["image/jpeg", "jpeg", "image", "png", "jpg", "bmp"],
  EXTENSIONSFILE: ["doc", "docx", "xls", "xlsx", "ppt", "pptx", "pdf", "zip", "rar", "txt"],
  EXTENSIONSVIDEO: ["video/quicktime", "quicktime", "qt", "mov"],
  EXTENSIONSAUDIO: ["audio/wav", "audio", "wav", "mp3"],
  ERROR_ECM_NO_AVAILABLE: 3,
  ACTION_DETAILS: "details",
  ACTION_EDIT: "edit" },
{

  /**
   * Whether or not is a native device
   * @var isNative boolean
   */
  isNative: false,

  /**
                    * Update or init the element data
                    * @param {} data
                    */
  initializeData: function initializeData(data) {
    // Call base
    this._super(data);

    this.isNative = bizagi.util.isCordovaSupported();
    this.fileData = [];
  },

  /**
      * Method to customize each control
      * @returns {}
      */
  renderControl: function renderControl() {
    var self = this;
    var template = this.renderFactory.getTemplate("ecm");

    // Set files property
    self.files = self.properties.value || [];
    self.filesCount = self.files.length;

    return this.renderItem(template, {});
  },

  /**
      * Method to render non editable values
      * @returns {*|jQuery}
      */
  renderReadOnly: function renderReadOnly() {
    var template = this.renderFactory.getTemplate("ecm-readonly");
    var data = {
      noFiles: this.filesCount === 0 };


    return this.renderItem(template, data);
  },

  /**
      * Method to render items
      * @returns {*|jQuery}
      */
  renderItem: function renderItem(template, data) {
    var self = this;
    var items = "";
    var html = $.fasttmpl(template, data);

    // Render current children
    for (var i = 0; i < self.filesCount; i++) {
      var item = self.renderUploadItem(self.files[i]);

      items += item;
    }

    html = self.replaceFilesHtml(html, items);

    return html;
  },

  /**
      * Renders a single upload item
      * @param file
      * @returns {*}
      */
  renderUploadItem: function renderUploadItem(file) {
    var self = this;
    var template = self.renderFactory.getTemplate("ecm-item");
    var form = self.getFormContainer();

    var data = {
      id: self.properties.id,
      idFileUpload: file[0],
      fileName: file[1],
      editable: self.properties.editable,
      xPath: self.properties.xpath,
      xpathContext: self.getXpathContext() || form.properties.xpathContext || "",
      idPageCache: self.properties.idPageCache,
      idAttrib: self.properties.idAttrib,
      sessionId: form.properties.sessionId };


    // Builds the upload item url
    var originalData = $.extend({}, data);
    data.url = self.buildItemUrl(originalData);

    // Define File Properties
    file.getFileProperties = $.when(self.renderFileLayout(data));

    var html = $.fasttmpl(template, data);

    return html;
  },

  /**
      * Method to get properties and metadata of ECM item
      * @param data
      * @returns {*}
      */
  renderFileLayout: function renderFileLayout(data) {
    var self = this;
    var form = self.getFormContainer();
    var deferred = new $.Deferred();

    var originalData = $.extend({}, data);

    $.when(
    self.dataService.metadataECMMultiaction().getECMMetadata(data),
    self.dataService.metadataECMMultiaction().getFileProperties(originalData)).
    done(function (metaData, fileProperties) {

      if (Array.isArray(fileProperties)) {
        fileProperties = fileProperties[0];
      }

      if (Array.isArray(metaData)) {
        metaData = metaData[0];
      }

      fileProperties.idAttrib = self.properties.idAttrib;
      fileProperties.showMetadata = self.properties.showMetadata;
      fileProperties.xpathContext = self.getXpathContext() || form.properties.xpathContext || "";
      fileProperties.xpath = self.properties.xpath;
      fileProperties.fileName = data.fileName;
      fileProperties.idPageCache = self.properties.idPageCache;
      fileProperties.fileData = originalData;

      var result = $.extend({}, fileProperties, metaData);

      deferred.resolve(result);

    });

    return deferred.promise();
  },

  /**
      * Replaces a {{files}} tag in the container for the specified "replace" element
      * @param html
      * @param replace
      * @returns {*}
      */
  replaceFilesHtml: function replaceFilesHtml(html, replace) {
    return html.replace("{{files}}", replace);
  },

  /**
      * Template method to implement in each device to customize each render after processed
      */
  postRender: function postRender() {
    var $control = this.getControl();

    this.$contentFile = $(".bz-file-upload__container", $control);
    this.$inputFile = $(".bz-file-upload__input", $control);
    this.$nativeFile = $(".bz-file-upload__native", $control);
  },

  configureHandlers: function configureHandlers() {
    this.checkFileExtensions();
    this.attachEvents();
  },

  /**
      * Method to customize the render's behaviour to add handlers when the control is readonly
      */
  configureHandlersReadOnly: function configureHandlersReadOnly() {
    var self = this;
    var $control = self.getControl();

    // Handler to download the file
    $("ul.bz-readonly-lists", $control).on("click", ".bz-file .bz-file-name", function () {
      var item = $(this).parent(".bz-file");
      var file = item.data();

      bizagi.loading.start();

      $.when(bizagi.util.media.downloadFile(file.url, file.title)).fail(function (error) {
        self.executeFormFailHandler(error);
      }).always(function () {
        bizagi.loading.stop();
      });
    });
  },

  /**
      * Send image using HTTP
      */
  saveImage: function saveImage() {
    var self = this;
    var dataItem = "";

    if (self.editedImageURL) {
      dataItem = self.editedImageURL;
    } else if (self.fileProperties) {
      dataItem = self.fileProperties.url;
    }

    // Fix android 4.4 getting images from recent folder
    var dataFile = bizagi.util.media.getImagePath(dataItem);
    var data = {
      dataFile: dataFile,
      fileName: self.fileProperties.name,
      mimeType: "image/jpeg" };


    // Cleans variables related previous uploads
    self.editedImageURL = null;
    self.fileProperties = null;

    return this.uploadFile(data);
  },

  /**
      * Method to save a video file
      * @param {} dataVideo
      * @returns {}
      */
  saveVideo: function saveVideo(dataVideo) {
    var data = {
      dataFile: dataVideo[0].fullPath,
      fileName: dataVideo[0].name,
      mimeType: "video/quicktime" };


    return this.uploadFile(data);
  },

  /**
      * Method to save a file
      * @param data
      * @returns {*|void}
      */
  uploadFile: function uploadFile(data) {
    var self = this;
    var params = {};
    var addParams = self.buildAddParams();

    addParams.queueID = "q_" + bizagi.util.encodeXpath(self.getUploadXpath());
    addParams["xPath"] = addParams["xPath"];
    addParams.Filename = data.fileName;

    params.data = addParams;
    params.options = new FileUploadOptions();
    params.options.fileKey = "file";
    params.options.fileName = data.fileName;
    params.options.uploadKey = { key: "Filedata" };
    params.options.addUrl = self.properties.addUrl;
    params.dataFile = data.dataFile;

    if (data.mimeType)
    params.options.mimeType = data.mimeType;

    return $.when(bizagi.util.media.uploadFile(params)).then(function (result) {
      self.onUploadFileCompleted(JSON.parse(decodeURIComponent(result.response)));
    }).fail(function (error) {
      self.executeFormFailHandler(error);
    });
  },

  /**
      * Method to configure the actions with control
      * @return {}
      */
  attachEvents: function attachEvents() {
    var self = this;
    var $control = self.getControl();

    // Handlers to management of menu actions
    $.each(self.files, function (i, file) {
      self.attackMenuEvents(file);
    });

    // Attach all events
    this.$contentFile.off("click").on("click", function (evt) {
      self.chooseFile();
    });

    // Handler to download the file
    $("ul.bz-files", $control).on("click", ".bz-file .bz-file-name", function () {
      var item = $(this).parent(".bz-file");
      var file = item.data();

      bizagi.loading.start();

      $.when(bizagi.util.media.downloadFile(file.url, file.title)).fail(function (error) {
        self.executeFormFailHandler(error);
      }).always(function () {
        bizagi.loading.stop();
      });
    });


    if (self.isNative) {
      // Upload files
      this.$nativeFile.off("click").actionSheet({
        actions: self.getUploadChoices(),
        actionClicked: function actionClicked(action) {
          self.handlerUploadFile(action.guid);
        },
        appear: function appear(action) {
          self.triggerGlobalHandler("closeModalValidationMessage", {});
          self.triggerGlobalHandler("modalViewDidAppear", {});
        },
        cancelClicked: function cancelClicked(action) {
          self.triggerGlobalHandler("modalViewDidDisappear", {});
        } });


      // Resolution upload
      $(".bz-file-upload__resolution", $control).off("click").actionSheet({
        actions: bizagi.util.media.getImageResolutionSizes(),
        withoutCancel: true,
        actionClicked: function actionClicked(action) {
          bizagi.loading.start();

          $.when(self.handlerResolutionUpload(action.guid)).always(function () {
            bizagi.loading.stop();
          });
        },
        appear: function appear(action) {
          self.triggerGlobalHandler("closeModalValidationMessage", {});
          self.triggerGlobalHandler("modalViewDidAppear", {});
        },
        cancelClicked: function cancelClicked(action) {
          self.triggerGlobalHandler("modalViewDidDisappear", {});
        } });

    } else {

      // Attach event to web upload-control
      this.$inputFile.off("change").on("change", function () {
        self.addFile();
        this.value = "";
      });

      // Just in case
      this.$inputFile.off("click").on("click", function (evt) {
        evt.stopPropagation();
      });
    }
  },

  /**
      * Update menu options
      * @param $fileItem
      * @param fileProperties
      */
  addMenuEvents: function addMenuEvents($fileItem, fileProperties) {
    var self = this;
    var viewMore = $fileItem.find(".bz-file-menu");
    var items = [];

    if (fileProperties.showMetadata) {
      items.push({
        name: self.getResource("render-collection-navigator-details-form"),
        fn: function fn() {
          self.processActionByType(self.Class.ACTION_DETAILS, fileProperties, $fileItem);
        } });

    }

    if (fileProperties.showMetadata && fileProperties.allowUpdateMetadata)
    items.push({
      name: self.getResource("render-collection-navigator-edit-form"),
      fn: function fn() {
        self.processActionByType(self.Class.ACTION_EDIT, fileProperties, $fileItem);
      } });


    if (fileProperties.allowCheckOut) {
      items.push({
        name: self.getResource("render-ecm-bt-lock"),
        fn: function fn() {
          self.lockECMFile($fileItem, fileProperties);
        } });

    }

    if (fileProperties.allowCancelCheckOut) {
      items.push({
        name: self.getResource("render-ecm-bt-unlock"),
        fn: function fn() {
          self.unlockECMFile($fileItem, fileProperties);
        } });

    }

    /*if (fileProperties.allowUpdateContent) {
          items.push({
              name: self.getResource("render-ecm-bt-upgrade"),
              fn: function () {
                  self.processActionByType("upgrade", fileProperties);
              }
          });
      }*/

    if (fileProperties.allowDelete) {
      items.push({
        name: self.getResource("render-ecm-bt-delete"),
        fn: function fn() {
          self.deleteECMFile($fileItem, fileProperties.idFileUpload);
        } });

    }

    // TODO: Check items has data
    viewMore.overflowMenu({ items: items });
  },

  /**
      * Removes a file from the ECM control
      * @param $fileItem
      * @param fileId
      */
  deleteECMFile: function deleteECMFile($fileItem, fileId) {
    var self = this;

    bizagi.showConfirmationBox(this.getResource("render-ecm-confirm-message")).
    done(function () {
      $.when(self.deleteUploadItem($fileItem, fileId)).
      done(function () {
        // Detach item
        $fileItem.hide();
        $fileItem.remove();

        // Update value
        self.setValue(self.files, false);

        // Check maxFiles
        self.checkMaxFiles();

        // Trigger change
        self.triggerRenderChange();
      });
    });
  },

  /**
      * Check maximum number of files
      */
  checkMaxFiles: function checkMaxFiles() {
    if (this.isMaxFilesReached()) {
      $(this.$contentFile).addClass("bz-file-upload__container--hidden");
    } else {
      $(this.$contentFile).removeClass("bz-file-upload__container--hidden");
    }
  },

  /**
      * Check if the maximum number of files than can be uploaded has been reached
      * @returns {}
      */
  isMaxFilesReached: function isMaxFilesReached() {
    var maxFiles = this.properties.maxfiles;
    var actualFiles = this.properties.value ? this.properties.value.length : 0;

    return maxFiles !== 0 && this.filesCount >= maxFiles && actualFiles >= maxFiles;
  },

  /**
      * Call the form failhandler with an specific error
      * @param {} error
      * @returns {}
      */
  executeFormFailHandler: function executeFormFailHandler(error) {
    var errorResponse = "";

    if (error && error.code === this.Class.ERROR_ECM_NO_AVAILABLE) {
      errorResponse = this.getResource("render-ecm-service-not-available");
    } else if (error && error.responseText) {
      errorResponse = error.responseText;
    } else {
      errorResponse = this.getResource("render-ecm-service-general-error");
    }

    this.getFormContainer().failHandler(errorResponse);
  },

  lockECMFile: function lockECMFile($fileItem, fileProperties) {
    var self = this;

    $.when(self.lockUploadItem(fileProperties.idFileUpload)).done(
    function (result) {
      var file = [result.idFileUpload, fileProperties.fileName];
      var newItem = self.renderUploadItem(file);

      $fileItem.replaceWith(newItem);
      self.attackMenuEvents(file);
    });
  },

  unlockECMFile: function unlockECMFile($fileItem, fileProperties) {
    var self = this;

    $.when(self.unlockUploadItem(fileProperties.idFileUpload)).
    done(function (result) {
      var file = [result.idFileUpload, fileProperties.fileName];
      var newItem = self.renderUploadItem(file);

      $fileItem.replaceWith(newItem);
      self.attackMenuEvents(file);
    });
  },

  /**
      * Method to add new file from native or hybrid environment
      */
  chooseFile: function chooseFile() {
    if (this.isNative) {
      this.$nativeFile.click();
    } else if (bizagi.util.media.fileAPISupported()) {
      this.$inputFile.click();
    }
  },

  /**
      * Check allow extensions and define possible choices
      * @returns {}
      */
  checkFileExtensions: function checkFileExtensions() {
    var self = this;
    var validExtensions = self.properties.validExtensions || "";

    self.validOptions = { video: false, image: false, file: false };

    // Preprocess the extensions string
    validExtensions = validExtensions.replace(/\s+/g, "").toLowerCase();

    if (typeof validExtensions === "undefined" || validExtensions === "") {
      self.validOptions = { video: true, image: true, file: true };
    } else {
      validExtensions = validExtensions.split(";");

      if (validExtensions.length === 1 && validExtensions[0].indexOf("*.*") !== -1) {
        self.validOptions.video = true;
        self.validOptions.image = true;
      } else {
        for (var i = 0, length = validExtensions.length; i < length; i++) {
          validExtensions[i] = validExtensions[i].replace("*.", "");

          if (self.Class.EXTENSIONSFILE.toString().indexOf(validExtensions[i]) >= 0) {
            self.validOptions.file = true;
          } else if (self.Class.EXTENSIONSIMG.toString().indexOf(validExtensions[i]) >= 0) {
            self.validOptions.image = true;
          } else if (self.Class.EXTENSIONSVIDEO.toString().indexOf(validExtensions[i]) >= 0) {
            self.validOptions.video = true;
          }
        }
      }
    }

    // Media is not supported then upload control is disabled
    if (!self.validOptions.file && !self.validOptions.image && !self.validOptions.video) {
      $(self.$contentFile).addClass("bz-file-upload__container--hidden");
    }

    // Limit of files is reached then upload control is disabled
    if (self.isMaxFilesReached() || !self.isNative && !bizagi.util.media.fileAPISupported()) {
      $(this.$contentFile).addClass("bz-file-upload__container--hidden");
    }
  },

  /**
      * Add upload options
      */
  getUploadChoices: function getUploadChoices() {
    var self = this;
    var itemsUpload = [];

    // Take photo, Choose photo
    if (self.validOptions.image) {
      itemsUpload.push({
        "guid": 1,
        "displayName": bizagi.localization.getResource("workportal-actionsheet-upload-take-photo") });

      itemsUpload.push({
        "guid": 2,
        "displayName": bizagi.localization.getResource("workportal-actionsheet-upload-choose-photo") });

    }

    // Take video
    if (self.validOptions.video) {
      itemsUpload.push({
        "guid": 3,
        "displayName": bizagi.localization.getResource("workportal-actionsheet-upload-take-video") });

    }

    // Stores the upload options
    return [].concat(itemsUpload);
  },

  /**
      * Handler of upload files
      * @param {} ordinal
      * @returns {}
      */
  handlerUploadFile: function handlerUploadFile(ordinal) {
    var self = this;
    var actionSelected = ordinal || 0;

    switch (actionSelected) {
      case 2:

        navigator.camera.getPicture(function (dataImage) {
          self.dataImage = dataImage;
          var extensions = self.Class.EXTENSIONSIMG.concat(self.Class.EXTENSIONSVIDEO);

          bizagi.loading.start();

          $.when(bizagi.util.media.checkMaxSize(dataImage, self.properties)).
          then(function () {
            // Gets the real properties for the file
            return $.when(bizagi.util.media.getFileDataInfo(self.dataImage)).
            then(function (fileData) {
              self.fileProperties = fileData;

              if (bizagi.util.media.checkFileTypes(fileData, self.properties, extensions)) {
                if (!bizagi.util.isAndroid()) {
                  return $.when(bizagi.util.media.modalResolutionUpload()).then(function (buttonIndex) {
                    if (buttonIndex <= bizagi.util.media.getImageResolutionItems().length) {
                      return self.handlerResolutionUpload(buttonIndex);
                    }
                  });

                } else {
                  // Force click to show actionsheet menu
                  $(".bz-file-upload__resolution", self.getControl()).click();
                }
              }
            });
          }).always(function () {
            bizagi.loading.stop();
          });

        }, self.onFail.bind(self), {
          quality: self.Class.QUALITY_PICTURE,
          sourceType: Camera.PictureSourceType.PHOTOLIBRARY });

        break;
      case 1:
        navigator.camera.getPicture(function (dataImage) {
          bizagi.loading.start();

          $.when(bizagi.util.media.checkMaxSize(dataImage, self.properties)).then(function () {
            return $.when(bizagi.util.media.getFileDataInfo(dataImage)).then(function (fileData) {
              self.fileProperties = fileData;
              return self.saveImage(dataImage);
            });
          }).always(function () {
            bizagi.loading.stop();
          });
        },
        self.onFail.bind(self), {
          quality: self.Class.QUALITY_PICTURE,
          sourceType: navigator.camera.PictureSourceType.CAMERA,
          mediaType: navigator.camera.MediaType.PICTURE,
          destinationType: Camera.DestinationType.FILE_URI,
          correctOrientation: true,
          targetWidth: 1280,
          targetHeight: 960 });

        break;
      case 3:
        navigator.device.capture.captureVideo(function (dataVideo) {
          bizagi.loading.start();

          $.when(bizagi.util.media.checkMaxSizeVideo(dataVideo, self.properties)).
          then(function () {
            return self.saveVideo(dataVideo);
          }).always(function () {
            bizagi.loading.stop();
          });

        }, self.onFail.bind(self), { limit: self.Class.LIMIT });
        break;}

  },

  /**
      * Catch errors
      * @param {} error
      */
  onFail: function onFail(error) {
    var messageError = bizagi.util.processFailMessage(error);

    bizagi.loading.stop();

    if (messageError && typeof messageError === "string" && messageError.indexOf("No Activity found to handle Intent") !== -1) {
      var hasResource = bizagi.util.isValidResource("render-upload-error-media-app");
      var message = hasResource ? bizagi.localization.getResource("render-upload-error-media-app") : "Your device does not have an app to execute this action.";

      self.performValidationMessage(message);
    }
  },

  /**
      * Adds a validation message to the render
      * @param message
      */
  performValidationMessage: function performValidationMessage(message) {
    var fail = bizagi.util.processFailMessage(message);

    bizagi.showMessageBox(fail);
  },

  /**
      * Handler of resolution upload
      * @param {} ordinal
      * @returns {}
      */
  handlerResolutionUpload: function handlerResolutionUpload(ordinal) {
    var self = this;
    var actionSelected = ordinal || 0;

    // If original, save image normally, if not, reduce quality and size
    if (actionSelected === bizagi.util.media.ResolutionType.ORIGINAL) {
      return self.saveImage(self.dataImage);
    } else {
      var resolution = bizagi.util.media.getImageResolution(actionSelected);

      return $.when(bizagi.util.media.transformImageSize(self.dataImage, resolution)).then(function (result) {

        self.editedImageURL = result.localUrl;
        self.properties.url = result.base64Image;

        return self.saveImage(self.dataImage);
      });
    }
  },

  /**
      * Method to add new file
      */
  addFile: function addFile() {
    var self = this;
    var dataFile = this.$inputFile[0].files[0];
    var extensions = self.Class.EXTENSIONSIMG.concat(self.Class.EXTENSIONSFILE, self.Class.EXTENSIONSVIDEO);

    dataFile.fullPath = this.$inputFile[0].value;
    bizagi.loading.start();

    if (bizagi.util.media.checkMaxSizeFile(dataFile, self.properties) && bizagi.util.media.checkFileTypes(dataFile, self.properties, extensions)) {

      $.when(self.processUploadFile(dataFile)).done(function (result) {
        if (!bizagi.util.isFailResponse(result)) {
          self.onUploadFileCompleted(JSON.parse(decodeURIComponent(result)));
        } else {
          self.performValidationMessage(result);
        }
      }).fail(function (error) {
        self.executeFormFailHandler(error);
      }).always(function () {
        bizagi.loading.stop();
      });
    } else {
      bizagi.loading.stop();
    }
  },

  /**
      * Build add params to send to the server
      * @param {} dataFile
      */
  buildAddFileParams: function buildAddFileParams(dataFile) {
    var self = this;
    var prefix = self.Class.BA_CONTEXT_PARAMETER_PREFIX;

    // Get form data for POSTing
    var formData = new FormData();
    var form = self.getFormContainer();
    var data = self.buildAddParams();

    // Build add params to send to the server
    var keys = Object.keys(data);

    keys.map(function (key) {
      formData.append(key, data[key]);
    });

    formData.append("Filedata", dataFile);
    formData.append("Filename", dataFile.name.substr(dataFile.name.replace(/([\\\/]+)/g, "/").lastIndexOf("/") + 1));
    formData.append(self.Class.BA_ACTION_PARAMETER_PREFIX + "sessionId", form.properties.sessionId);

    if (self.properties.contexttype)
    formData.append(prefix + "contexttype", self.properties.contexttype);

    try {
      if (typeof BIZAGI_SESSION_NAME !== "undefined") {
        formData.append(BIZAGI_SESSION_NAME, form.properties.sessionId);
      } else {
        formData.append("JSESSIONID", form.properties.sessionId);
      }
    } catch (e) {
      formData.append("JSESSIONID", form.properties.sessionId);
    }

    return formData;
  },

  /**
      * Process an upload file into the server
      * @param {} dataFile
      * @returns {}
      */
  processUploadFile: function processUploadFile(dataFile) {
    var data = {
      url: this.properties.addUrl,
      formData: this.buildAddFileParams(dataFile) };


    return this.dataService.processUploadFile(data);
  },

  /**
      * onUploadFileCompleted
      * @param {} result
      */
  onUploadFileCompleted: function onUploadFileCompleted(result) {
    var self = this;
    var $wrapper = $(".bz-files", self.getControl());

    if (result.idFileUpload && result.fileName) {
      var file = [result.idFileUpload, result.fileName];
      var newItem = self.renderUploadItem(file);

      // Locate it before the upload wrapper
      $wrapper.append(newItem);

      // Copy the files value but not its reference
      var files = self.files.slice();

      //Add the new item to this temporal array
      files.push(file);

      // Update value
      self.setValue(files, true);

      // Check maxFiles
      self.checkMaxFiles();
      self.attackMenuEvents(file);

      self.editedImageURL = null;
    } else {
      self.performValidationMessage(result.message);
    }
  },

  attackMenuEvents: function attackMenuEvents(file) {
    var self = this;
    var $control = self.getControl();

    if (file && file.getFileProperties) {
      $.when(file.getFileProperties).done(function (fileProperties) {
        // TODO: Fail scenario...
        var $fileItem = $("." + self.properties.id + "-" + fileProperties.idFileUpload, $control);

        self.fileData[fileProperties.idFileUpload] = fileProperties.metadata || [];
        self.addMenuEvents($fileItem, fileProperties);
      });
    }
  },

  processActionByType: function processActionByType(type, fileData, $fileItem) {
    var self = this;
    var properties = self.properties;
    var form = self.getFormContainer();

    bizagi.loading.start();

    // Instantiate slide form object
    var slideView = new bizagi.rendering.smartphone.slide.ecm(self.dataService, self.renderFactory, {
      title: self.properties.displayName || "",
      container: form.container,
      navigation: self.getParams().navigation,
      editable: properties.editable && type !== self.Class.ACTION_DETAILS,
      orientation: properties.orientation || "ltr",
      onSave: function onSave(result) {
        if (result.update) {
          var file = [result.idFileUpload, result.fileName];
          var newItem = self.renderUploadItem(file);

          $fileItem.replaceWith(newItem);
          self.attackMenuEvents(file);
        }
      },
      onCancel: function onCancel() {
        // console.log("CANCEL");
      } });


    slideView.render({ metadata: fileData.metadata, fileData: fileData }).
    always(function () {
      bizagi.loading.stop();
    });

  } });