/**
 *   Name: Bizagi Smartphone slide Grid AdHoc implementation
 *   Author: UI Forms Team
 *   Comments:
 *   -   Serves as an slide view that will show hidden columns for a grid
 */

// Extends itself
$.Class.extend('bizagi.rendering.smartphone.slide.adhocForm', {}, {

  /* CONSTRUCTOR
                                                                      =====================================================*/
  init: function init(renderFactory, slideFormParams) {
    var self = this;

    // Define instance variables
    this.renderFactory = renderFactory;
    this.slideFormDeferred = new $.Deferred();
    this.slideFormParams = slideFormParams || {};

    // Create container
    self.container = self.slideFormParams.navigation.createRenderContainer({ title: self.slideFormParams.title });
  },

  /**
      * Render the grid view form
      * The params are the same that will be send to the ajax service
      * @param params
      * @returns Returns a deferred
      */
  render: function render(params) {
    var self = this;

    // Fill content
    self.renderSlideForm(params);
    // Return promise
    return self.slideFormDeferred.promise();
  },

  /* RENDERS slideForm
      =====================================================*/
  renderSlideForm: function renderSlideForm(params) {

    var self = this;
    var data = self.transformData(params);

    // Load template and data
    self.form = self.renderFactory.getContainer({
      type: "form",
      data: data.form,
      navigation: this.slideFormParams.navigation,
      view_container: self.container });


    self.form.buttons = [$.extend(self.form.buttons[0] || {},
    {
      "caption": bizagi.localization.getResource("render-form-dialog-box-save"),
      "actions": ["submitData", "refresh"],
      "submitData": true,
      "refresh": true,
      "ordinal": 0,
      "action": "save",
      "save": true,
      "style": "",
      // This callback avoid to call the default processButton in the form
      callback: function callback() {
        if (self.form.validateForm()) {
          self.goBack();
          // Resolve deferred with form data
          var data = {};
          self.form.collectRenderValues(data);
          self.slideFormDeferred.resolve(data);
        }
      } }),

    $.extend(self.form.buttons[1] || {}, {
      "caption": bizagi.localization.getResource("render-form-dialog-box-close"),
      "actions": ["submitData", "refresh"],
      "submitData": true,
      "refresh": true,
      "ordinal": 1,
      "action": "back",
      "save": true,
      "style": "",
      //This callback avoid to call the default processButton in the form
      callback: function callback() {
        // Close slide
        self.goBack();
        self.slideFormDeferred.reject();
      } })];



    // Add save button if the form is editable
    if (!self.slideFormParams.allowEdition) {
      self.form.buttons.shift();
    }

    $.when(self.form.render()).
    done(function (element) {

      if (params.row[0] == "mock_id") {// If it is an add form
        $.each(data.elementsWithDefaultValue, function (i, item) {
          var render = self.form.getRender(item.xpath + "." + item.id);
          if (!render) {
            render = self.form.getRender(item.xpath);
          }
          var defaultValue = bizagi.util.isArray(item.defaultValue) ? item.defaultValue[0] : item.defaultValue;
          render.setDisplayValue(defaultValue);
        });
      }

      // Append form in the slide view
      self.container.element.html(element);

      self.slideFormParams.navigation.navigate(self.container.id);

      //self.container.setNavigationButtons(self.form);

      // Remove default form buttons
      $(".ui-bizagi-button-container .action-button", self.form.container).remove();

      // Add Button
      $(".ui-bizagi-button-container", self.form.container).append(self.buttonContainer);

      // Publish an event to check if the form has been set in the DOM
      self.form.triggerHandler("ondomincluded");
    });
  },

  /**
      * Transform data into a JSON standard rendering format
      * @param params
      * @returns {[]}
      */
  transformData: function transformData(params) {
    var self = this;
    var row = params.row;
    var columns = params.columns;
    var isAddForm = false;

    // Set xpath context
    var xpathContext = params.xpathContext.length > 0 ?
    params.xpathContext + "." + params.xpath + "[" + params.row[0] + "]" :
    params.xpath + "[id=" + params.row[0] + "]";

    var data = {
      type: "form",
      form: {
        elements: [],
        pageCacheId: params.pageCacheId,
        sessionId: params.sessionId,
        properties: {
          xpathContext: xpathContext },

        actions: self.transformActions(params.xpath, params.actions),
        validations: self.transformValidations(params.xpath, params.validations) },

      elementsWithDefaultValue: [] };


    if (row[0] === "mock_id") {
      isAddForm = true;
    }

    $.each(row, function (i, item) {

      // Skip first data (key not need to be rendered)
      if (i > 0) {
        var columnProperties = columns[i - 1].properties;
        var element = { render: { properties: $.extend({}, columnProperties) } };

        // Hack properties
        element.render.properties.type = self.mapRenderType(element.render.properties.type);
        element.render.properties.defaultvalue = typeof element.render.properties.defaultvalue !== "undefined" ? element.render.properties.defaultvalue : element.render.properties.defaultValue;
        element.render.properties.displayType = 'both';
        if (self.slideFormParams.allowEdition == false) element.render.properties.editable = false;
        element.render.properties.value = self.mapValue(element.render.properties.type, item);
        if (element.render.properties.recalculate) element.render.properties.data = null;

        // Check defaultValue property
        if (typeof element.render.properties.defaultvalue !== "undefined") {
          if (typeof element.render.properties.value === "undefined" || element.render.properties.value == null) {
            //element.render.properties.value = element.render.properties.defaultvalue;
            data.elementsWithDefaultValue.push({
              id: element.render.properties.id,
              xpath: element.render.properties.xpath,
              defaultValue: element.render.properties.defaultvalue });


            delete element.render.properties.defaultvalue;
            delete element.render.properties.defaultValue;
          }
        }

        // Check columnVisible property
        if (typeof element.render.properties.columnVisible !== "undefined") {
          element.render.properties.visible = element.render.properties.columnVisible;
        }

        // Fix readonly columns
        if (element.render.properties.type === "readonly") {
          element.render.properties.type = "text";
          element.render.properties.editable = false;
        }

        // Fix upload columns
        if (element.render.properties.type === "upload") {
          element.render.properties.isColumn = true;
        }

        // Fix search columns
        if (element.render.properties.type === "search") {
          var value = element.render.properties.value;

          if (value == null) {
            element.render.properties.value = {};
          } else if (value.length > 0) {
            value = [].concat(value[0]);
            element.render.properties.value = {
              id: value[0],
              label: value[1],
              additionallabel: value[2] || undefined };

          }
        }
        var isDisabledControl = ["document", "formLink", "upload", "uploadecm", "image", "search"].includes(element.render.properties.type);
        var isExclusiveCheck = (element.render.properties.isExclusive || false) && "boolean" === element.render.properties.type;

        if (isExclusiveCheck || isAddForm && isDisabledControl) {
          element.render.properties.editable = false;
        } else if (isExclusiveCheck) {
          element.render.properties.value = value;
          element.render.properties.editable = false;
        }

        // Add element
        data.form.elements.push(element);
      }

    });

    return data;
  },

  /**
      * Map column renderType into normal render
      * @param type
      * @returns {string}
      */
  mapRenderType: function mapRenderType(type) {
    type = type.replaceAll("smartphone", "").replaceAll("column", "");

    //some exceptions
    if (type === "FormLink") return "formLink";

    type = type.toLowerCase();
    return type;
  },

  /**
      * Method to translate grid values into render values
      * @param type
      * @param value
      * @returns {[]}
      */
  mapValue: function mapValue(type, value) {
    if (["combo", "radio", "list"].includes(type)) {
      if (value && value.length > 0) {

        // Transform the value property
        return [{
          id: value[0][0],
          value: value[0].length > 2 ? $.grep(value[0], function (item, i) {
            return i !== 0;
          }) : value[0][1] }];


      } else {
        return null;
      }
    }

    // Else: Don't do anything
    return value;
  },

  /**
      * Parse form actions into just grid form actions
      * @param xpath
      * @param actions
      * @returns {[]}
      */
  transformActions: function transformActions(xpath, actions) {
    var self = this;
    var gridActions = [];

    if (bizagi.util.isEmpty(actions)) return gridActions;

    $.each(actions, function (index, action) {
      var bDependencyFound = false;
      for (var i = 0; i < action.dependencies.length; i++) {
        if (action.dependencies[i].indexOf(xpath) != -1) {
          bDependencyFound = true;
        }
      }

      if (bDependencyFound) {
        var newAction = JSON.parse(JSON.encode(action));

        // Replace dependencies
        for (var j = 0; j < newAction.dependencies.length; j++) {
          newAction.dependencies[j] = newAction.dependencies[j].replaceAll(xpath + "[].", "");
        }

        // Replace conditions
        self.transformConditions(xpath, newAction.conditions);

        // Replace commands
        self.transformCommands(xpath, newAction.commands);
        self.transformCommands(xpath, newAction.elseCommands);

        // Add to grid actions
        gridActions.push(newAction);
      }
    });

    return gridActions;
  },

  /**
      * Parse form validations into just grid form validations
      * @param xpath
      * @param validations
      * @returns {[]}
      */
  transformValidations: function transformValidations(xpath, validations) {
    var self = this;
    var gridValidations = [];

    if (bizagi.util.isEmpty(validations)) return gridValidations;

    $.each(validations, function (index, validation) {
      var newValidation = JSON.parse(JSON.encode(validation));

      // Replace conditions
      self.transformConditions(xpath, newValidation.conditions);

      // Add to grid actions
      gridValidations.push(newValidation);

    });

    return gridValidations;
  },

  /**
      * Parse a single condition or a complex condition in order to prepare a grid action/validation
      * @param xpath
      * @param condition
      */
  transformConditions: function transformConditions(xpath, condition) {
    var self = this;

    if (condition.expressions) {
      for (var index in condition.expressions) {
        self.transformConditions(xpath, condition.expressions[index]);
      }
    } else if (condition.simple) {
      self.transformConditions(xpath, condition.simple);
    } else if (condition.complex) {
      self.transformConditions(xpath, condition.complex);
    } else {
      if (condition.xpath) {
        condition.xpath = condition.xpath.replaceAll(xpath + "[].", "");
        if (condition.argumentType === 'xpath') {
          condition.argument = condition.argument.replaceAll(xpath + "[].", "");
        }
      } else {
        // Apply to button and label controls
        if (condition && condition.renderId) {
          condition.renderId = condition.renderId.replaceAll(xpath + "[].", "");
        }
      }
    }
  },

  /**
      * Parse a command action to prepare a grid action
      * @param xpath
      * @param commands
      */
  transformCommands: function transformCommands(xpath, commands) {
    var refreshIndex = null;
    if (commands == null || commands.length === 0) return;

    for (var i = 0; i < commands.length; i++) {
      if (commands[i].command === "refresh") {
        refreshIndex = i;
      } else {
        if (commands[i].xpath) {
          commands[i].xpath = commands[i].xpath.replaceAll(xpath + "[].", "");
        }
        if (commands[i].argumentType === 'xpath') {
          commands[i].argument = commands[i].argument.replaceAll(xpath + "[].", "");
        }

        if (!commands[i].xpath && commands[i].renderId) {
          commands[i].renderId = commands[i].renderId.replaceAll(xpath + "[].", "");
        }
      }
    }

    if (refreshIndex != null) {
      commands.splice(i, 1);
    }
  },

  /**
      * Go back and destroy
      */
  goBack: function goBack() {
    this.container.destroy();
  } });