/**
 *   Name: Bizagi smartphone Render Link Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the link render class to adjust to smartphoe devices
 */

// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.link", bizagi.rendering.link.prototype);
// Extends itself
bizagi.rendering.smartphone.link.extend("bizagi.rendering.smartphone.link", {}, {

  /**
                                                                                   * Template method to implement in each device to customize each render after processed
                                                                                   * @returns {}
                                                                                   */
  postRender: function postRender() {
    var $control = this.getControl();
    this.link = $control.find(".bz-link");

    this._super();

    this.preventDefaultForAnchorTag();
  },

  /**
      * Method to customize each render after processed in read-only mode
      */
  postRenderReadOnly: function postRenderReadOnly() {
    var $control = this.getControl();
    this.link = $control.find(".bz-link");

    this._super();
  },

  /**
      * Sets the value in the rendered control
      * @param {} value
      * @returns {}
      */
  setDisplayValue: function setDisplayValue(value) {
    // Set internal value
    this.setValue(value, false);

    if (this.isValidValue()) {
      // Get the decoded version of the URL
      value = getFullyDecodedURI($.trim(value));

      this.link.attr("href", value);
    }
  },

  /**
      * Template method to implement in each device to customize the render's behaviour to add handlers
      * @returns {}
      */
  configureHandlers: function configureHandlers() {
    this.attachEvents();
  },

  /**
      * Template method to implement in each device to customize the render's behaviour
      * to add handlers when the control is readonly
      */
  configureHandlersReadOnly: function configureHandlersReadOnly() {
    this.attachEvents();
  },

  /**
      * Configure event handlers
      */
  attachEvents: function attachEvents() {
    var self = this;

    if (self.isValidValue()) {
      self.link.on("click", function (evt) {
        evt.preventDefault();
        evt.stopPropagation();

        var linkEncode = encodeURI(this.getAttribute("href"));

        self.linkSolver(linkEncode, self.properties.linktarget);
      });
    } else {
      self.link && self.link.addClass("bz-link--disabled");
    }
  },

  /**
      * Checks if the value is a valid url (http/https valid urls)
      * TODO: Check if this validation is necessary
      * @param {} value
      * @returns {}
      */
  isValidValue: function isValidValue() {
    if (!this.hasValue()) return false;
    var value = $.trim(this.getValue() || "");

    return value.toLowerCase().match(/^(http|https):\/\//) !== null || false;
  },

  /**
      * Executes the link
      * @param {} linkEncode
      * @param {} linkTarget
      * @returns {}
      */
  linkSolver: function linkSolver(linkEncode, linkTarget) {
    if (linkTarget === "newwindow" || bizagi.util.isCordovaSupported()) {
      window.open(linkEncode, "_system", "location=yes");
    } else {
      window.open(linkEncode, "_system");
    }
  },

  processLayout: function processLayout(isLabelFormat) {
    if (!isLabelFormat) {
      this._super(isLabelFormat, this.properties.textFormat);
    } else {
      if (typeof this.properties.textFormat.background !== "undefined") {
        this._super(isLabelFormat, { background: this.properties.textFormat.background });
      }
    }
  },

  getElementsToStylize: function getElementsToStylize($context) {
    return $(".bz-link", $context);
  } });