/*
 *   Name: Bizagi Smartphone Section Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the section class to adjust to smartphone devices
 */

// Extends from container tablet base
bizagi.rendering.smartphone.container.extend("bizagi.rendering.smartphone.section", bizagi.rendering.section.prototype);
// Auto extend
bizagi.rendering.smartphone.section.extend(
"bizagi.rendering.smartphone.section",
{},
{
  /*
   *   Render the container layout
   */
  renderContainer: function renderContainer() {
    var self = this;
    var properties = this.properties;
    var template = self.renderFactory.getTemplate("section");

    // Render the panel
    var html = $.fasttmpl(template, {
      expandedClassIcon: self.expandedIcon,
      displayName: properties.displayName,
      editable: properties.editable,
      orientation: properties.orientation,
      uniqueId: properties.uniqueId,
      isDesign: self.getMode() !== "execution",
      messageValidation: properties.messageValidation,
      guid: properties.guid,
      displayAs: properties.displayAs });


    // Render children
    html = self.replaceChildrenHtml(html, self.renderChildrenHtml());
    return html;
  },

  /**
      * Method to customize the container's behaviour to add handlers
      */
  configureHandlers: function configureHandlers() {
    var self = this;
    //Fix para establecer la orientación correcta en el modal
    var dir = self.getSectionOrientationRTL(self) ? "rtl" : "ltr";
    $(self.container).attr("dir", dir);

    self.bind("reopenSection", function () {
      self.expand();
    });

    $(">div.bz-section__content", self.container).mobileModal({
      displayName: self.properties.displayName,
      anchor: $(">div.bz-section__header", self.container),
      appendTo: self.container,
      dir: dir,
      onBeforeShow: function onBeforeShow(m) {
        self.addToHistoryNavigation();
        setTimeout(function () {
          var p = self.getSectionParent(self.parent);
          var isIphoneNative = bizagi.util.isiOSSmartphoneDevice() && bizagi.util.isNativePluginSupported();
          if (isIphoneNative) {
            bizagiapp.sectionDidAppear();
          } else {
            $("header[data-role=header]").css({ display: "none" });
            $("div[data-role=footer] .bz-form-buttons").css({ "display": "none" });
          }

          if (!self.isFirstLevel(true)) {
            $($("div[data-role=header]", p.container)[0]).css({ display: "none" });
          }
        }, 250);
      },

      onClose: function onClose(m) {
        if (m) m.close();
        self.removeFromHistoryNavigation();
        var p = self.getSectionParent(self.parent);
        if (self.isFirstLevel()) {
          var isIphoneNative = bizagi.util.isiOSSmartphoneDevice() && bizagi.util.isNativePluginSupported();

          if (isIphoneNative) {
            bizagiapp.sectionDidDisappear();
          } else {
            $('header[data-role=header]').css({ "display": "block" });
            $("div[data-role=footer] .bz-form-buttons").css({ "display": "flex" });
          }
        } else {
          $($("div[data-role=header]", p.container)[0]).css({ display: "flex" });
        }
      } });

  },

  /**
      * Return true if the section is on the main form, or false if it is nested.
      */
  isFirstLevel: function isFirstLevel(isOpen) {
    var form = this.getFormContainer();
    var params = form.getParams() || {};
    var selected = params.selectedSection;
    if (typeof selected === "undefined") return true;
    return isOpen ? selected.length <= 1 : selected.length === 0;
  },

  /**
      * Set the selected section opened
      */
  addToHistoryNavigation: function addToHistoryNavigation() {
    var form = this.getFormContainer();
    var params = form.getParams() || {};
    if (typeof params.selectedSection === "undefined") {
      form.setParam("selectedSection", []);
      params = form.getParams();
    }
    params.selectedSection.push(this.properties.id);
  },

  /**
      * Unset the actual section as a opened section
      */
  removeFromHistoryNavigation: function removeFromHistoryNavigation() {
    var form = this.getFormContainer();
    var params = form.getParams() || {};
    if (typeof params.selectedSection !== "undefined") {
      params.selectedSection.pop();
    }
  },

  /**
      * Check if section its open
      *
      * @return boolean
      */
  isOpen: function isOpen() {
    return this.container.hasClass("bz-section--collapsed") ? false : true;
  },

  /**
      * Focus on container
      */
  focus: function focus() {
    // Expand container
    this.expand();

    // Call base
    this._super();
  },

  /**
      * Shows slide section container
      * @returns {}
      */
  expand: function expand() {
    $(">div.bz-section__header", this.container).trigger("click");
  },

  /**
      * Closes slide section container
      * @returns {}
      */
  collapse: function collapse() {
    if (this.isOpen()) {
      $(">div.bz-section__header", this.container).trigger("click");
    }
  },

  /**
      * Shows or closes the slide section container from actions
      * @param {} argument
      */
  toogleContainer: function toogleContainer(argument) {
    if (argument) {
      this.expand();
    } else {
      this.collapse();
    }
  } });