/*
 *   Name: Bizagi Smartphone Render list Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the activity checkList render class to adjust to smartphone devices
 */

// Extends from base list
bizagi.rendering.smartphone.checkList.extend("bizagi.rendering.smartphone.activityCheckList", {}, {

  /**
                                                                                                     * Update or init the element data
                                                                                                     * @param data
                                                                                                     */
  initializeData: function initializeData(data) {
    // Call base
    this._super(data);
    var goal = this.properties.activityDescription && this.properties.activityDescription !== "" ?
    bizagi.localization.getResource("workportal-project-plan-activity-goal") + ": " + this.properties.activityDescription : "";
    this.properties.displayName = goal !== "" ? goal : bizagi.localization.getResource("workportal-project-plan-activity-work-title");
    this.properties.displayType = "vertical";
  },
  /*
      *   Template method to implement in each device to customize each render after processed
      */
  postRender: function postRender() {
    var self = this;
    var $control = self.getControl();
    // Call base
    this._super();

    self.newItemName = $(".bz-checkList__input", $control);
    self.addEditButton = $(".add-edit-item-button-checklist", $control);
    self.cancelEditButton = $(".cancel-edit-item-button-checklist", $control);

    self.additionalElements();
    self.notifyLoadItems();
  },

  notifyLoadItems: function notifyLoadItems() {
    var self = this;
    setTimeout(function () {
      var resolvedItems = self.properties.items.filter(function (i) {
        return i.resolved;
      }).length;
      bizagi.util.setItemLocalStorage("newChanges", JSON.stringify({
        'hasChanges': true,
        'idActivity': self.properties.idActivity,
        'itemsResolved': resolvedItems,
        'items': self.properties.items,
        'progress': self.properties.activityWork }));


      bizagi.webpart.publish("onLoadDataItemsFromFormActivityPlan", { "items": self.properties.items, "activityWork": self.properties.activityWork });
    }, 100);
  },

  additionalElements: function additionalElements() {
    var self = this;

    if (self.addEditButton && self.addEditButton.length > 0) {
      self.addEditButton.hide();
    }
    if (self.cancelEditButton && self.cancelEditButton.length > 0) {
      self.cancelEditButton.hide();
    }
    //control.parent().addClass("render-activity-check");
  },

  configureHandlers: function configureHandlers() {
    var self = this;
    var control = self.getControl();
    this._super();
    $("[data-action=add]", control).unbind("click").click(function () {
      var itemSelected = $(".bz-checkList__item.bz-checkList__item--selected", self.control);
      if (itemSelected.length <= 0) {
        self.prepareNewItem();
      } else {
        $(".bz-checkList__input", itemSelected).focus();
      }
    });

    $("[data-action=delete]", self.control).bind("click", function () {
      var that = this;
      var message = bizagi.localization.getResource("workportal-widget-reports-confirm");
      var contentHolder = $(this).closest(".bz-checkList__item");

      contentHolder.addClass("ui-bizagi-to-remove");

      $.when(bizagi.showConfirmationBox(message, "Bizagi", "info")).
      done(function () {

        var itemId = $(that).closest(".bz-checkList__item").data("item-guid");

        self.deleteItem(itemId);
      }).fail(function () {
        contentHolder.removeClass("ui-bizagi-to-remove");
      });
    });

    $("label[data-action=edit-item]", self.control).bind("click", function () {
      var element = $(this).closest(".bz-checkList__item");
      var itemId = element.data("item-guid");
      var itemPosition = element.data("item-position");

      self.selectItem(itemId, itemPosition);
    });
  },

  selectItem: function selectItem(itemId) {
    var self = this;

    for (var i = 0, length = self.properties.items.length; i < length; i++) {
      if (self.properties.items[i].guid === itemId) {
        self.properties.items[i].selected = true;
      } else {
        self.properties.items[i].selected = false;
      }
    }

    self.repaintControl();

    var itemSelected = $(".bz-checkList__item.bz-checkList__item--selected", self.getControl());
    var itemSelectedName = $(".bz-checkList__input", itemSelected);
    var itemContent = itemSelectedName.val();

    itemSelectedName.focus();
    itemSelectedName[0].setSelectionRange(itemContent.length, itemContent.length);

    self.addSelectedHandlers();
  },

  deleteItem: function deleteItem(itemId) {
    var self = this;

    for (var i = 0, length = self.properties.items.length; i < length; i++) {
      if (self.properties.items[i].guid === itemId) {
        self.properties.items.splice(i, 1);
        break;
      }
    }

    self.setValue(self.properties.items);
    self.repaintControl();
    self.addSelectedHandlers();
  },

  prepareNewItem: function prepareNewItem() {
    var self = this;
    var newItem = {
      guid: Math.guid(),
      resolved: false,
      name: "",
      selected: true };


    self.properties.items.push(newItem);
    self.repaintControl();
    self.newItemName.focus();

    self.newItemName.blur(function () {
      var itemSelected = $(".bz-checkList__item.bz-checkList__item--selected", self.control);
      var itemSelectedName = $(".bz-checkList__input", itemSelected);
      var valueInput = itemSelectedName.val();

      if (valueInput === "" && self.properties.items && self.properties.items.length >= 1 &&
      self.properties.items.slice(-1)[0].name === "") {
        self.properties.items.pop();
      }
    });
    self.addSelectedHandlers();
    self.newItemName.keypress(function (event) {
      if (event.which == 13) {
        event.preventDefault();
        self.addItem();
        self.prepareNewItem();
      }
    });
  },

  addSelectedHandlers: function addSelectedHandlers() {
    var self = this;
    var itemSelected = $(".bz-checkList__item.bz-checkList__item--selected", self.control);
    var itemSelectedName = $(".bz-checkList__input", itemSelected);
    itemSelectedName.focus();
    $("[data-action=accept-edit]", itemSelected).bind("click", function () {
      var newItemName = itemSelectedName.val();
      if (newItemName && newItemName !== "") {
        self.addItem();
      }
    });


    $("[data-action=cancel-edit]", itemSelected).bind("click", function (e) {
      var newItemName = itemSelectedName.val();
      var element = $(this).closest(".bz-checkList__item");
      var itemPosition = element.data("item-position");
      var guidItem = element.data("item-guid");

      if (newItemName && newItemName !== "" && self.properties.items[itemPosition].name !== "") {
        self.properties.items[itemPosition].selected = false;
      }
      if (newItemName === "" || self.properties.items[itemPosition].name === "") {
        self.deleteItem(guidItem);
      }
      //self.addButton.show();
      self.repaintControl();
    });
  },

  addItem: function addItem() {
    var self = this;
    var $control = self.getControl();

    var itemSelected = $(".bz-checkList__item.bz-checkList__item--selected", $control);
    var itemSelectedName = $(".bz-checkList__input", itemSelected);
    var newItemName = itemSelectedName.val();

    $.each(self.properties.items, function (index, item) {
      if (item.selected) {
        item.name = newItemName;
        item.selected = false;
      }
    });
    self.updateOriginalValue();
    self.setValue(self.properties.items);
    self.repaintControl();
  },

  repaintControl: function repaintControl() {
    var self = this;
    var checkListHtml = self.renderControl();
    $("[data-component=checkList]", self.control).replaceWith(checkListHtml);
    self.postRender();
    self.configureHandlers();
  },

  onCheckListGroupChange: function onCheckListGroupChange(itemId, checked) {
    var self = this;
    var item;
    var items = $.grep(self.properties.items, function (element, index) {
      return element.guid === itemId;
    });
    if (items && items.length > 0) {
      item = items[0];
    }
    item.resolved = checked;
    self.setValue(self.properties.items);
    self.refreshValue(self.properties.items);
  },

  refreshValue: function refreshValue(items) {
    var self = this,
    length = items.length,
    completed = 0;

    for (var i = 0; i < length; i++) {
      if (items[i].resolved) {
        completed += 1;
      }
    }

    if (length > 0) {
      self.properties.activityWork = Math.floor(100 * completed / length);
    } else
    {
      self.properties.activityWork = 100;
    }

    self.notifyLoadItems();
  },

  getValue: function getValue() {
    var self = this;
    return self.value || [];
  },

  controlValueIsChanged: function controlValueIsChanged() {
    var self = this;
    var originalValue = JSON.stringify(self.properties.originalValue);
    var newValue = JSON.stringify(self.value);
    return originalValue !== newValue;
  },

  /*
      *   Updates original value for some comparisons
      */
  updateOriginalValue: function updateOriginalValue() {
    var self = this;
    self.properties.originalValue = bizagi.clone(self.value);
  },

  /*
      *   Add the render data to the given collection in order to send data to the server
      */
  collectData: function collectData(renderValues) {
    var self = this;
    var properties = self.properties;

    // Add the render value
    var xpath = properties.xpath;
    var value = self.getValue();
    var idActivity = self.properties.idActivity;

    if (self.controlValueIsChanged()) {
      // Filter by valid xpaths and valid values
      if (!bizagi.util.isEmpty(xpath) && !bizagi.util.isEmpty(idActivity)) {
        if (Array.isArray(value)) {
          renderValues[properties.xpath] = {
            idActivity: idActivity,
            items: value };

        } else if (value === "") {
          renderValues[properties.xpath] = "";
        }
      }
    } else {
      bizagi.util.setItemLocalStorage("newChanges", JSON.stringify({
        'hasChanges': false }));

    }
  },

  isValid: function isValid(invalidElements) {
    var self = this,
    properties = self.properties,
    message;
    var items = self.getValue();
    message = self.getResource("render-activity-check-list-validation");
    for (var i = 0; i < items.length; i += 1) {
      var item = items[i];
      if (!item.resolved) {
        invalidElements.push({
          xpath: properties.xpath,
          message: message });

        break;
      }
    }
  } });