/**
 *   Name: Bizagi Smartphone Render Search List Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the search-list render class to adjust to smartphone devices
 */

// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.searchList", bizagi.rendering.searchList.prototype);
// Extends itself
bizagi.rendering.smartphone.searchList.extend("bizagi.rendering.smartphone.searchList", {
  SEARCH_MIN_LENGTH: 2,
  SEARCH_MAX_RECORDS: 50,
  SEARCH_DELAY: 250,
  SEARCH_ELEMENTS: {} },
{

  renderControl: function renderControl() {
    var self = this;
    var properties = self.properties;
    var template = self.renderFactory.getTemplate("searchList");

    return $.fasttmpl(template, {
      id: properties.id,
      value: properties.value.map(function (el) {
        return self.formatItem(el.value);
      }).join(", ") });

  },

  /**
      * Template method to implement in each device to customize each render after processed
      * @returns {}
      */
  postRender: function postRender() {
    this.searchListInput = $(".bz-search-list__input", this.getControl());
  },

  postRenderReadOnly: function postRenderReadOnly() {
    this.searchListInput = $(".bz-search-list__input", this.getControl());
  },

  renderReadOnly: function renderReadOnly() {
    var self = this;
    var template = self.renderFactory.getTemplate("searchList.readonly");

    // Render template
    var content = $.fasttmpl(template, {
      items: self.properties.value || {},
      editable: self.properties.editable || true },
    {
      formatItem: self.formatItem });


    return content;
  },

  /**
      * Template method to implement in each device to customize the render's behaviour
      * to add handlers
      * @returns {}
      */
  configureHandlers: function configureHandlers() {
    var self = this;

    // Select new item
    self.searchListInput.on("click", function (evt) {
      var parent = $(evt.target).parent() || {};
      if (parent.hasClass('bz-pill')) return;

      self._searchListViewModal();
    });
  },

  _searchListViewModal: function _searchListViewModal() {
    var self = this;
    var header = self.getHeaderTemplate();
    var content = self.getContentTemplate();
    var footer = self.getFooterTemplate();

    self.searchListInput.mobileView({
      customHeader: header,
      orientation: self.properties.orientation || "",
      cssClassHeader: "bz-modal-view__header-multiple",
      contentTemplate: content,
      footerTemplate: footer,
      transition: "slideIn:up",
      close: function close() {
        $(window).unbind("resize");
        self.triggerGlobalHandler("modalViewDidDisappear", {});
      },
      afterOpenAnimation: function afterOpenAnimation(event) {
        var $context = $(".bz-modal-view__search", event.view.header);

        // Fix the moving content to top
        if ($context && $context[0]) {
          $context[0].scrollIntoView(true);
          $context.focus();
        }
      },
      open: function open() {
        self.triggerGlobalHandler("closeModalValidationMessage", {});
        self.triggerGlobalHandler("modalViewDidAppear", {});
      },
      configureActions: function configureActions(context) {
        self.adjustResize();
        self.configureModalViewHandlers(context);
      } });

  },

  /**
      * Sets the value in the rendered control
      * @param {} value
      * @returns {}
      */
  setDisplayValue: function setDisplayValue(value) {
    var self = this;
    var finalValue = typeof value === "string" ? JSON.parse(value).value : value.value;

    // Set internal value
    self.setValue(finalValue);

    if (value !== undefined) {
      if (self.properties.editable) {
        self.searchListInput.html(finalValue.map(function (el) {
          return self.formatItem(el.value);
        }).join(", "));
      }
    }
  },

  /**
      * Remove a item from the list
      * @param {} element
      * @returns {}
      */
  deleteItem: function deleteItem(element) {
    var self = this;
    var item = element.parent('div');

    // Remove from main memory and set the new value
    self.removeElement({ id: item.data("id") });

    // Remove list element
    item.remove();
  },

  selectItem: function selectItem(item) {
    var templateItem = this.getTagTemplate(item, this.properties.editable);
    this.modalViewTagList.append(templateItem);
  },

  /**
      * Configure the modalView Handlers for the new suggest control
      * @param {} inputContainer
      * @returns {}
      */
  configureModalViewHandlers: function configureModalViewHandlers(modalView) {
    var self = this;
    var modalFooter = modalView.footer;
    var modalHeader = modalView.header;

    // Initialize data
    var elements = {};

    if (self.hasValue()) {
      var list = self.getListItems();
      $.each(list, function (index, item) {
        elements[item.id] = item;
      });
    }

    // Configuring list as a selectable element
    self.modalViewList = $(".bz-list", modalView.content);
    self.modalViewTagList = $(".bz-search-pill__results", modalHeader);

    var $clearButton = $(".bz-modal-view__clear", modalHeader);

    // Suggest
    var $suggestInput = $(".bz-modal-view__search", modalHeader);
    var suggestFilter = bizagi.util.debounce(function () {
      if (this.value === "") {
        $clearButton.removeClass("bz-modal-view__clear--visible");
      } else {
        $clearButton.addClass("bz-modal-view__clear--visible");
      }
      self.autoCompleteSearchResults($.trim(this.value));
    }, this.Class.SEARCH_DELAY);

    $suggestInput.on("change keyup", suggestFilter).on("focus", function () {
      $(".bz-search__header", modalView.header).addClass("bz-search__header--active");
    }).on("blur", function () {
      $(".bz-search__header", modalView.header).addClass("bz-search__header--active");
    });

    //Clear the search
    $clearButton.bind("click", function () {
      $suggestInput.val("").trigger("change");
    });

    $("[data-action=close]", modalView.header).bind("click", function () {
      modalView.close();
    });

    // Add tags to list
    self.modalViewList.on("click", "li", function () {
      var item = $.data(this).item;

      // Prevent duplicate elements
      if (!item || item && elements[item.id]) return;
      elements[item.id] = item;

      // Update state of button
      $(modalFooter).find('.bz-btn').removeClass("bz-btn--secundary");
      $(modalFooter).find('.bz-btn').addClass("bz-btn--primary");

      // Update list
      if ((Object.keys(elements) || []).length === 1) {
        $(modalHeader).find(".bz-search-pill__selected ").
        removeClass("bz-search-pill__selected--hidden");
      }

      self.selectItem(item);
    });

    // Remove tags of list
    self.modalViewTagList.on("click", '.bz-pill > .bz-btn-icn', function (evt) {
      evt.preventDefault();
      evt.stopPropagation();

      var element = $(this);
      var tag = element.parent('div');

      // Update state of button
      $(modalFooter).find('.bz-btn').removeClass("bz-btn--secundary");
      $(modalFooter).find('.bz-btn').addClass("bz-btn--primary");

      // Remove from main memory and set the new value
      delete elements[tag.data("id")];

      // Remove list element
      tag.remove();

      // Update list
      if ((Object.keys(elements) || []).length === 0) {
        $(modalHeader).find(".bz-search-pill__selected ").
        addClass("bz-search-pill__selected--hidden");
      }
    });

    $(modalFooter).delegate("[data-action=save]", "click", function (evt) {
      // Create a temporal array
      var newList = [];
      $.each(elements, function (index, item) {
        newList.push(item);
      });

      self.setDisplayValue({ value: newList });

      modalView.close();
    });
  },

  autoCompleteSearchResults: function autoCompleteSearchResults(currentValue) {
    var self = this;

    $.when(self.processRequest({ term: currentValue })).
    done(function (response) {

      self.modalViewList.find("li").remove();

      for (var i = 0, length = response.length; i < length; i++) {
        var searchItem = self.renderSearchItem(response[i]);

        searchItem.appendTo(self.modalViewList);
      }
    });
  },

  renderSearchItem: function renderSearchItem(item) {
    var self = this;
    var template = self.renderFactory.getTemplate("searchList.item");

    var searchItem = $.tmpl(template, {
      id: item.id,
      label: item.label,
      value: this.formatItem(item.value) });


    searchItem.data("item", item);

    return searchItem;
  },

  /**
      * Process the request data for the autocomplete control
      * @param {} req
      * @returns {}
      */
  processRequest: function processRequest(req) {
    var self = this;
    var properties = self.properties;
    var deferred = new $.Deferred();

    // Create array for response objects   
    var suggestions = [];

    if (req.term.length >= this.Class.SEARCH_MIN_LENGTH) {

      // Update term property to reflect the search
      properties.term = req.term;

      // Cancel previous request in order to improve the performance
      if (self.deferredReference && typeof self.deferredReference.reject === "function" &&
      self.deferredReference.state() !== "resolved") {
        self.deferredReference.reject({ hideStatus: true });
      }

      self.deferredReference = self.getData().done(function (data) {
        if (data && data.length > 0) {
          data = data.slice(0, properties.maxRecords);

          if (data.length > self.Class.SEARCH_MAX_RECORDS) {
            data = data.slice(0, self.Class.SEARCH_MAX_RECORDS);

            suggestions.push({
              id: self.Class.NO_RESULTS_SEARCH_ID,
              label: self.getResource("render-search-maximum-records-allowed"),
              value: "" });



          } else {
            data = data.slice(0, properties.maxRecords);
          }

          $.each(data, function (i, val) {
            if (properties.maxRecords === 0 || i < properties.maxRecords) {
              var parseValue = bizagi.util.performTermMatches(val.value, req.term);

              suggestions.push({ id: val.id, label: parseValue, value: val.value });
            }
          });

        } else {
          suggestions.push({
            id: self.Class.NO_RESULTS_SEARCH_ID,
            label: self.getResource("render-grid-no-records"),
            value: "" });

        }

        deferred.resolve(suggestions);

      }).fail(function () {
        deferred.reject();
      });

    } else {
      deferred.resolve(suggestions);
    }

    return deferred.promise();
  },

  getContentTemplate: function getContentTemplate() {
    var self = this;
    var value = self.getValue();
    var finalValue = typeof value === "string" ? JSON.parse(value).value : value.value;

    var template = this.renderFactory.getTemplate("searchList.items");
    var content = $.fasttmpl(template, {
      items: finalValue || {},
      editable: self.properties.editable || true },
    {
      formatItem: self.formatItem });


    return $.trim(content);
  },

  getFooterTemplate: function getFooterTemplate() {
    var self = this;
    var template = self.renderFactory.getTemplate("searchList.actions");
    var content = $.fasttmpl(template, {
      items: self.properties.value || {},
      editable: self.properties.editable || true });


    return $.trim(content);
  },

  getTagTemplate: function getTagTemplate(item, editable) {
    var template = this.renderFactory.getTemplate("searchList.tag");
    var content = $.fasttmpl(template, {
      id: item.id,
      label: item.label,
      value: this.formatItem(item.value),
      editable: editable });

    return $.trim(content);
  },

  adjustResize: function adjustResize() {
    var self = this;
    var initialScreenSize = window.innerHeight;

    $(window).off("resize").on("resize", bizagi.util.debounce(function () {
      var that = $(this);

      // Hide the footer
      if (that.height() < initialScreenSize) {
        $(".bz-mobile-view__footer_content").addClass("bz-hidden");
      } else {
        $(".bz-mobile-view__footer_content").removeClass("bz-hidden");
      }
    }, self.Class.SEARCH_DELAY));
  },

  getHeaderTemplate: function getHeaderTemplate() {
    var self = this;
    var value = self.getValue();
    var finalValue = typeof value === "string" ? JSON.parse(value).value : value.value;
    var template = this.renderFactory.getTemplate("searchList-modal-header");

    var content = $.fasttmpl(template, {
      items: finalValue || {},
      editable: self.properties.editable || true,
      emptyItems: (finalValue || []).length === 0 },
    {
      formatItem: self.formatItem });


    return $.trim(content);
  } });