/*
 *   Name: Bizagi Smartphones Group Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the group class to adjust to smartphones devices
 */

// Extends from container smartphone base
bizagi.rendering.smartphone.container.extend("bizagi.rendering.smartphone.group", bizagi.rendering.group.prototype);
// Auto extend
bizagi.rendering.smartphone.group.extend("bizagi.rendering.smartphone.group", {}, {

  /**
                                                                                    * Method to customize the container's behaviour to add handlers
                                                                                    */
  configureHandlers: function configureHandlers() {
    var self = this;
    $(">h5.bz-collapsible__header", self.container).bind("click", function () {
      $(self.container).toggleClass("bz-collapsible--collapsed");
    });
  },

  /**
      * Template method to implement in each device to customize each container after processeds
      * @param {} group      
      */
  postRenderContainer: function postRenderContainer(group) {
    // Call base
    this._super(group);
    if (this.properties.collapse) {
      this.collapse();
    }
  },

  /**
     * Check if group its open
     * 
     * @return boolean
     */
  isOpen: function isOpen() {
    return this.container.hasClass("bz-collapsible--collapsed") ? false : true;
  },

  /**
      * Focus on container     
      */
  focus: function focus() {
    // Expand container
    this.expand();

    // Call base
    this._super();
  },

  /**
      * Expands group container 
      * @returns {} 
      */
  expand: function expand() {
    if (!this.isOpen()) {
      $(">h5.bz-collapsible__header", this.container).trigger("click");
    }
  },

  /**
      * Collapses group container 
      * @returns {} 
      */
  collapse: function collapse() {
    if (this.isOpen()) {
      $(">h5.bz-collapsible__header", this.container).trigger("click");
    }
  },

  /**
      * Expands or collapse the container from actions
      * @param {} argument      
      */
  toogleContainer: function toogleContainer(argument) {
    if (argument) {
      this.expand();
    } else {
      this.collapse();
    }
  } });