/**
 *   Name: Bizagi Smartphone Render Text Extension
 *   Author: UI Forms Team
 *   Comments: Extended text implementation
 */

// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.extendedText", bizagi.rendering.extendedText.prototype);
// Extends itself
bizagi.rendering.smartphone.extendedText.extend("bizagi.rendering.smartphone.extendedText", {
  minLines: 2,
  maxLines: 8,
  maxCharactersToShow: 100 },
{

  //#region Side effects functions

  /**
   * Template method to implement in each device to customize each render after processed
   */
  postRender: function postRender() {
    var self = this;
    self._super();
    self.textarea = self.getControl().find("textarea");
    if (self.textarea) self.textarea.css({ resize: "vertical" });
  },

  /**
      * Update or init the element data
      * @param {} data
      */
  initializeData: function initializeData(data) {
    // Call base
    this._super(data);
    var maxLines = this.properties.maxLines;
    this.properties.maxLines = maxLines > this.Class.maxLines ? this.Class.maxLines : maxLines < this.Class.minLines ? this.Class.minLines : maxLines;

    // Check if offline forms is supported
    this.isOfflineForm = bizagi.util.isOfflineForm({ context: this });
  },

  /**
      * Method to render non editable values
      */
  renderReadOnly: function renderReadOnly() {
    var template = this.renderFactory.getTemplate("extendedText-readOnly");
    var value = this.getValue();
    value = typeof value !== "undefined" && value !== null ? bizagi.util.sanitizeHTML(value) : "";
    var showChar = this.Class.maxCharactersToShow;

    var startString = value.substr(0, showChar);
    var endString = value.substr(showChar - 1, value.length - showChar);

    var data = {
      "value": value,
      "startString": startString,
      "endString": endString };


    return $.fasttmpl(template, data);
  },

  /**
      * Sets the value in the redered control
      * @param {} value
      */
  setDisplayValue: function setDisplayValue(value) {
    var self = this;
    var decodedValue = bizagi.util.decodeURI(value);
    decodedValue = !bizagi.util.isEmpty(decodedValue) && typeof decodedValue !== "string" ? decodedValue.toString() : decodedValue;
    value = self.validateValue(typeof decodedValue == "string" ? decodedValue : "", self.properties.maxLength);

    // Call base        
    self.setValue(value, false);

    // Set value in input
    if (self.properties.editable) {
      self.textarea.val(value);
    } else {
      var encodedText = bizagi.util.encodeHtmlText(self.getDisplayValue());
      $(".bz-textarea-readonly", self.getControl()).html(encodedText);
    }
  },

  /**
      * Implement to customize the render's behaviour to add handlers
      */
  configureHandlers: function configureHandlers() {
    var self = this;

    // Call base
    self._super();

    // Define max length of element

    // Cuando el tipo de dato es extended manda como maxlength 0 por lo que la validacion no puede ir sobre todo el metodo
    if (self.properties.maxLength > 0) {
      self.textarea.attr("maxlength", self.properties.maxLength);
      self.actualMaxLength = self.properties.maxLength;
    }

    $(self.textarea).bind("focus keyup paste", function (e) {
      if (e.which === 8 || e.which === 46) {
        var currentRow = self.textarea.prop("rows");
        if (currentRow > self.properties.maxLines) {
          self.textarea.prop("rows", self.properties.maxLines);
        }
      }
      self.updateTextareaSize(this);
    });

    $(self.textarea).bind("blur", function (e) {
      self.textarea.prop("rows", self.properties.maxLines);
    });
  },

  /**
      * Sets the functions when the control is readonly
      */
  configureHandlersReadOnly: function configureHandlersReadOnly() {
    var self = this;

    var $control = this.getControl();
    $(".bz-textarea-readonly", $control).on("click", ".bz-read-more", function (e) {
      if ($(e.delegateTarget).hasClass("bz-textarea-readonly--active")) {
        $(e.delegateTarget).removeClass("bz-textarea-readonly--active");
        $(this).html(self.getResource("workportal-project-discussion-comment-viewmore"));
      } else {
        $(e.delegateTarget).addClass("bz-textarea-readonly--active");
        $(this).html(self.getResource("workportal-project-discussion-comment-viewless"));
      }
      return false;
    });
  },

  /**
      * Changes control's height when it's auto extended
      * @param {Object} control HTML Object
      */
  updateTextareaSize: function updateTextareaSize(textarea) {
    if (this.properties.autoExtend) {
      var currentRows = $(textarea).prop("rows");
      if (textarea.scrollHeight > textarea.offsetHeight && currentRows >= this.Class.minLines && currentRows < this.Class.maxLines) {
        //Extend the rows
        $(textarea).prop("rows", currentRows + 1);
        this.updateTextareaSize(textarea);
      }
    }
  },

  /**
      * Handler of retype double actions
      */
  attachRetypeDouble: function attachRetypeDouble() {
    var self = this;
    var element = self.element;

    var input = $("input, textarea", element);
    if (!input || input.length === 0) return;

    // Apply blur handler to check value
    input.blur(function () {
      var control = $(this);
      var previousValue = self.previousValue;
      var value = self.value;

      if (previousValue != null && value != previousValue) {
        if ($(this).data("newValue") != undefined && control.val() != $(this).data("newValue")) {
          $(this).data().newValue = undefined;
        }
      }

      self.retypeDouble(control);
    });
  },

  /**
      * Retype actions
      * @param {} control
      */
  retypeDouble: function retypeDouble(control) {
    var self = this;
    var currentValue = control.val();
    var isVisible = $(control).attr("type") !== "hidden" && $(control).css("display") !== "none" && $(control).css("visibility") !== "hidden";

    if (isVisible) {
      if (!control.data("oldValue") || $(this).data("oldValue") === "") {

        // Check that there is something in the value or if a value has already been set
        if (currentValue === "" || currentValue === control.attr("newValue")) return;

        self.createTooltip(control, currentValue, self.getResource("render-number-retype"));

      } else {
        // Check that there is something in the value
        if (currentValue === "") return;

        if (currentValue !== control.data("oldValue")) {
          self.setValue("");

          self.setValidationMessage(self.getResource("render-text-retype-error"));
          control.val("");

          setTimeout(function () {
            control.focus();
          }, 100);

        } else {
          control.data("newValue", currentValue);
        }
        control.data("oldValue", "");
        if (control.data('ui-tooltip')) control.tooltip('destroy');
      }
    }
  },

  getElementsToStylize: function getElementsToStylize($context) {
    return this.properties.editable ? $("textarea", $context) : $(".bz-textarea-readonly", $context);
  },

  /**
      * Helptext tooltip
      * @param {} control
      * @param {} currentValue
      * @param {} currentTitle
      */
  createTooltip: function createTooltip(control, currentValue, currentTitle) {
    this._super(control, currentValue, currentTitle);

    $('body').bind('touchmove', function (e) {
      $('body').unbind('touchmove');
      if (control.data('ui-tooltip')) control.tooltip('destroy');
    });
  },

  setValidationMessage: function setValidationMessage(message) {
    // Call base
    this._super(message);

    if (this.cloneRender) {
      this.cloneRender.setValidationMessage(message);
    }
  }

  //#endregion Side effects functions
});