/**
 *   Name: Bizagi Smartphone Render Check
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the Yes-No render class to adjust to smartphone devices
 */

// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.check", bizagi.rendering.check.prototype);
// Extends itself
bizagi.rendering.smartphone.check.extend("bizagi.rendering.smartphone.check", {}, {

  /**
                                                                                     * Template method to implement in each device to customize each render after processed
                                                                                     */
  postRender: function postRender() {
    var $control = this.getControl();
    this.check = $control.find("input");

    // Call base
    this._super();
  },

  /**
      * Method to render non editable values
      * @returns {*|jQuery}
      */
  renderReadOnly: function renderReadOnly() {
    var template = this.renderFactory.getTemplate("readonly");

    return $.fasttmpl(template, { value: this.getDisplayValue() });
  },

  /**
      * Method to customize each render after processed in read-only mode
      */
  postRenderReadOnly: function postRenderReadOnly() {
    var $control = this.getControl();
    this.check = $control.find("input");

    // Call base
    this._super();
  },

  /**
      * Sets the value in the rendered control
      * @param {} value
      */
  setDisplayValue: function setDisplayValue(value) {
    // Render as simple value
    var displayValue = bizagi.util.parseBoolean(value);

    // Set internal value
    this.setValue(displayValue, false);

    if (this.properties.editable && this.check) {
      if (displayValue) {
        this.check.attr("checked", "checked");
      } else {
        this.check.removeAttr("checked");
      }
    } else if (this.check) {
      this.check.val(this.getDisplayValue());
    }
  },

  /**
      * Method to customize the render's behaviour to add handlers
      */
  configureHandlers: function configureHandlers() {
    var self = this;

    this.check.on("click", function (evt) {
      var isCheck = $(this).is(':checked');

      self.setValue(isCheck, true);

    });
  },

  /**
      * Cleans current data
      */
  cleanData: function cleanData() {
    this.setDisplayValue(false);
    this.setValue(false, false);
  } });