/**
*   Name: BizAgi Smartphone Render Cascading combo Extension
*   Author: Forms team
*   Comments:
*   -   This script will redefine the cascading combo render class to adjust to tablet devices
*/

// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.cascadingCombo", bizagi.rendering.combo.prototype);
// Extends itself
bizagi.rendering.smartphone.cascadingCombo.extend("bizagi.rendering.smartphone.cascadingCombo", bizagi.rendering.cascadingCombo.prototype);
// Extends itself
bizagi.rendering.smartphone.cascadingCombo.extend("bizagi.rendering.smartphone.cascadingCombo", {
  // Statics
  BA_ACTION_PARAMETER_PREFIX: bizagi.render.services.service.BA_ACTION_PARAMETER_PREFIX,
  BA_CONTEXT_PARAMETER_PREFIX: bizagi.render.services.service.BA_CONTEXT_PARAMETER_PREFIX },
{

  /*
  *   Template method to implement in each children to customize each control
  */
  renderControl: function renderControl() {
    var mode = this.getMode();

    // Check if this is a dependant combo
    this.parentCombo = this.getParentCombo();
    if (this.parentCombo != null) {

      if (/cascadingcombo/i.test(this.parentCombo.properties.type)) {
        this.isDependant = true;
      }
    }

    // Apply element plugin, set a flag to avoid dependant combo ajax requests
    this.initializingCascadingCombo = true;

    if (mode != "execution") {
      this.initializingCascadingCombo = false;
    }

    return bizagi.rendering.smartphone.combo.prototype.renderControl.apply(this, arguments);
  },

  postRender: function postRender() {
    bizagi.rendering.smartphone.combo.prototype.postRender.apply(this, arguments);
  },

  /*
     *   Template method to implement in each device to customize the render's behaviour to add handlers
     */
  configureHandlers: function configureHandlers() {
    // Call base
    this._super();

    // Call desktop.combo method
    bizagi.rendering.smartphone.combo.prototype.configureHandlers.apply(this, arguments);
  },

  /**
      * Override to avoid the trigguer
      */
  cleanData: function cleanData() {
    bizagi.rendering.smartphone.combo.prototype.cleanData.apply(this, arguments);
  },

  /*
     *   Refresh the combo, after it has been painted
     */
  refresh: function refresh(params, isInitialValue) {
    var self = this;
    // Wait until current element is rendered
    $.when(self.isRendered()).done(function () {
      // Clear current control
      var control = self.getControl();
      var mode = self.getMode();
      control.empty();

      // Render combo again
      $.when(self.renderControl()).then(function (html) {
        // Append new element
        control.html(html);
        // Post-render method
        self.postRender();

        if (mode == "execution") {
          self.configureHandlers();
        }

        // Set current value
        if (isInitialValue) {
          self.setDisplayValue(self.getValue());
        } else {
          self.setDisplayValue({
            "id": "",
            "value": "" });

        }

        if (self.deferredVisible) {
          self.deferredVisible.resolve();
        }

        self.trigger("select", [self.getValue(), isInitialValue]);
      });
    });
  },
  /*
     *   Sets the value in the rendered control
     */
  setDisplayValue: function setDisplayValue(value) {
    bizagi.rendering.smartphone.combo.prototype.setDisplayValue.apply(this, arguments);
  },

  processComboData: function processComboData() {
    bizagi.rendering.smartphone.combo.prototype.processComboData.apply(this, arguments);
  },

  validateValue: function validateValue() {
    return bizagi.rendering.smartphone.combo.prototype.validateValue.apply(this, arguments);
  },

  renderComboTemplate: function renderComboTemplate() {
    return bizagi.rendering.smartphone.combo.prototype.renderComboTemplate.apply(this, arguments);
  },

  configureModalViewHandlers: function configureModalViewHandlers() {
    bizagi.rendering.smartphone.combo.prototype.configureModalViewHandlers.apply(this, arguments);
  },

  configureCombo: function configureCombo() {
    bizagi.rendering.smartphone.combo.prototype.configureCombo.apply(this, arguments);
  },

  changeCombo: function changeCombo(valueItem, valueObjet) {
    bizagi.rendering.smartphone.combo.prototype.changeCombo.apply(this, arguments);
  },

  /*
     *   Returns the selected value in the template
     */
  getSelectedValue: function getSelectedValue() {
    return bizagi.rendering.smartphone.combo.prototype.getSelectedValue.call(this, arguments);
  },

  getTemplateName: function getTemplateName() {
    return "combo";
  },
  /**
      * Filtering list of elements
      * @returns {}
      */
  filterList: function filterList(search, $li) {
    return bizagi.rendering.smartphone.combo.prototype.filterList.call(this, search, $li);
  } });