/*
*   Name: Bizagi smartphone Upload Column Decorator Extension
*   Author: Bizagi UI Forms
*   Comments:
*   -   This script will redefine the upload column decorator class to adjust to smartphone devices
*/

// Extends from column
bizagi.rendering.smartphone.columns.column.extend("bizagi.rendering.smartphone.columns.upload", {}, {

  applyOverrides: function applyOverrides(decorated) {
    var self = this;
    var mode = self.getMode();

    self._super(decorated);

    if (mode != "design" && mode != "layout") {

      decorated.getUploadXpath = self.getUploadXpath;

      decorated.getXpathContext = function () {

        var surrogateKey = this.surrogateKey || "";
        var columnXpath = self.getUploadXpath(decorated);
        var columnXpathSplit = columnXpath.split(".");
        var nameFile = columnXpathSplit[columnXpathSplit.length - 1];
        var xpath = self.grid.properties.xpath + "[id=" + surrogateKey + "]";

        var xpathContext = self.properties.xpathContext.length > 0 ? self.properties.xpathContext + "." + xpath : xpath;

        return { xpathContext: xpathContext, nameFile: nameFile };
      };

      decorated.buildItemUrl = function (file) {
        var that = this;
        var form = that.getFormContainer();
        var context = that.getXpathContext();

        return that.dataService.getUploadFileUrl({
          idRender: that.properties.id,
          xpath: context.nameFile,
          xpathContext: context.xpathContext,
          idPageCache: that.properties.idPageCache,
          fileId: file.id,
          sessionId: form.properties.sessionId,
          contexttype: that.properties.contexttype });

      };

    }
  },

  /**
      * Returns the xpath to be used  
      * @param {} decorated 
      * @returns {} 
      */
  getUploadXpath: function getUploadXpath(decorated) {
    var self = decorated || this;

    return self.grid.properties.xpath + "[id=" + self.surrogateKey + "]." + self.properties.xpath;
  } });