/**
 *   Name: Bizagi Smartphone Column Decorator Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the column decorator class to adjust to smartphone devices
 */

// Extends itself
bizagi.rendering.smartphone.columns.column.extend("bizagi.rendering.smartphone.columns.image", {}, {

  applyOverrides: function applyOverrides(decorated) {
    var self = this;

    this._super(decorated);

    var propertiesGrid = self.grid.properties != "undefined" ? self.grid.properties : decorated.grid.properties;
    var xpath = propertiesGrid.xpath + "[id=" + decorated.surrogateKey + "]";
    //decorated.getUploadXpath = this.getUploadXpath;
    decorated.properties.xpathContext =
    self.properties.xpathContext.length > 0 ? self.properties.xpathContext + "." + xpath : xpath;
    decorated.setDisplayValue = function () {
      return true;
    };
  }
  /**
     * Returns the xpath to be used  
     * @returns {} 
     */
  /*getUploadXpath: function() {
            return this.grid.properties.xpath + "[id=" + this.surrogateKey + "]." + this.properties.xpath;
        }*/ });