// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.label", bizagi.rendering.label.prototype);
// Extends itself
bizagi.rendering.smartphone.label.extend("bizagi.rendering.smartphone.label", {}, {
  /**
                                                                                    * Method to render non editable values
                                                                                    */
  renderReadOnly: function renderReadOnly() {
    this.setValue(this.properties.displayName);
    return this._super();
  },
  /**
      * Sets the value in the rendered control
      */
  setDisplayValue: function setDisplayValue(value) {
    // DON'T DO ANYTHING
  } });