/*
*   Name: Bizagi Smartphone Form Extension
*   Author: UI Forms Team
*   Comments:
*   -   This script will redefine the form class to adjust to smartphone devices
*/

// Extends from container smartphone base
bizagi.rendering.smartphone.container.extend("bizagi.rendering.smartphone.form", bizagi.rendering.form.prototype);
// Auto extend
bizagi.rendering.smartphone.form.extend("bizagi.rendering.smartphone.form", {}, {

  init: function init(params) {
    var self = this;
    params = params || {};
    // Define variables
    self.warnings = {};
    self.errors = {};
    if (params.syncError) {
      params.data.properties.editable = params.isOfflineErrorMode !== true;
      var syncMessage = params.syncError.type == "business" ? params.syncError.message : undefined;
      if (!bizagi.util.isEmpty(syncMessage)) {
        try {
          syncMessage = $.makeArray(syncMessage);
          syncMessage.forEach(function (currentMessage) {
            self.errors[currentMessage] = currentMessage;
          });
        } catch (e) {
          console.error("syncErrorMessage could not be parsed as array", syncMessage);
          self.errors[syncMessage] = syncMessage;
        }
      }
      delete params.syncError;
    }
    self.isOfflineErrorMode = params.isOfflineErrorMode === true;
    self.showToastOnSuccessSync = params.showToastOnSuccessSync === true;
    self.removeSyncErrorOnSubmit = params.removeSyncErrorOnSubmit === true;
    // Call base
    this._super(params);
  },

  /**
      * Template method to implement in each device to customize each container after processed
      */
  postRenderContainer: function postRenderContainer(container) {
    var self = this;

    self._super(container);
    var buttons = self.getButtons();

    //disable overflow-scrolling touch untill the form is loaded QA-2178
    if (bizagi.util.isiOSTabletDevice()) {
      $(".km-native-scroller").css("-webkit-overflow-scrolling", "inherit");
    }

    // Show warnings
    if (!bizagi.util.isObjectEmpty(self.warnings)) {
      var warningContainer = $('<ul class="ui-bizagi-form-warnings"></ul>');
      for (var key in self.warnings) {
        warningContainer.append($('<li>' + key + '</li>'));
      }

      // Add button
      $("<button>" + "X" + "</button>").appendTo(warningContainer);

      // Append to body 
      warningContainer.prependTo(self.container);
      warningContainer.click(function () {
        warningContainer.detach();
      });
    }

    // Show errors
    if (!bizagi.util.isEmpty(self.errors)) {
      for (var ekey in self.errors) {
        setTimeout(function () {
          self.addValidationMessage(ekey);
        }, 1);
      }
    }

    //Set button length
    var lengthButtons = buttons ? buttons.length : 0;

    if (lengthButtons) {

      $(document).data("auto-save", "auto-save");

      //bind event auto-save
      $(document).unbind("save-form").bind("save-form", function (e, deferredSave) {
        if (self.allowOnlineActions()) {
          self.autoSaveEvents(deferredSave);
        } else {
          deferredSave.resolve();
        }
      });

      //bind event beforeunload
      $(window).unbind('beforeunload').bind('beforeunload', function (e) {
        var newData = {};
        self.collectRenderValues(newData);

        //if there are changes in the form show a message
        if (!$.isEmptyObject(newData) && $(document).data('auto-save'))
        return bizagi.localization.getResource("confirmation-savebox-message2");

        return;
      });
    }

    // Process Sections opened
    var formParams = this.getParams() || {};
    var selectedSections = $.extend([], formParams.selectedSection);
    this.setParam("selectedSection", []);
    if (selectedSections.length > 0) {
      selectedSections.forEach(function (id) {
        var section = self.getRenderById(id);
        if (typeof section !== "undefined") {
          section.expand();
        }
      });
    }
  },

  /**
      * Auto Save Events
      *
      * @param {} deferredSave
      * @param {} saveBox
      * @returns {}
      */
  autoSaveEvents: function autoSaveEvents(deferredSave, saveBox) {
    var self = this;
    var data = {};

    if (self.isOfflineErrorMode) {
      deferredSave.resolve();
      return;
    }

    self.collectRenderValues(data);

    if (!$.isEmptyObject(data)) {
      var isStartForm = self.properties.formtype === "startform";
      var message = isStartForm ? bizagi.localization.getResource("confirmation-savebox-message4") : bizagi.localization.getResource("confirmation-savebox-message1");

      var confirmation = isStartForm ? bizagi.showConfirmationBox(message, "Bizagi", "warning", [{ label: bizagi.localization.getResource("confirmation-box-ok"), action: "resolve" }, { label: bizagi.localization.getResource("confirmation-box-cancel"), action: "reject" }]) :
      bizagi.showSaveBox(message, "Bizagi", "warning");
      $.when(confirmation).
      done(function () {
        if (!isStartForm) {
          bizagi.loading.start();
          $.when(self.saveForm()).
          always(function () {
            if (self.params.isOfflineForm && self.params.offlineType === "task" && self.online) {
              var syncDeferred = new $.Deferred();
              var syncParams = $.extend({ deferred: syncDeferred }, self.params);
              $(document).trigger("tryPushCase.offline", syncParams);
              $.when(syncDeferred).
              then(function () {
                deferredSave.resolve();
              }).
              fail(function () {
                console.log("Auto-Save Sync Error", arguments);
                //Resolve the save to allow back to the inbox
                deferredSave.resolve();
              }).always(function () {
                bizagi.loading.stop();
              });
            } else
            {
              bizagi.loading.stop();
              deferredSave.resolve();
            }
          });
        } else {
          deferredSave.resolve();
        }
      }).fail(function () {
        if (isStartForm) {
          deferredSave.reject();
        } else {
          deferredSave.resolve();
        }
      });
    } else {
      deferredSave.resolve();
    }
  },

  /**
      * TEMPLATE METHOD TO GET THE BUTTONS OBJECTS
      */
  getButtons: function getButtons() {
    var self = this;

    if (self.getParams() && (handler = self.getParams().getButtons))
    handler(self);

    return $(".ui-bizagi-button-container :button", self.container);
  },

  processButtons: function processButtons() {
    var self = this;
    var params = self.getParams();

    if (params && params.processButtons) {
      var handler = params.processButtons;
      handler(self);
    }

    var properties = self.properties;
    properties.buttons = self.buttons;

    // To cache a submit Data Buttom if exist
    self.saveButton = null;
    self.nextButton = null;

    // Set saveButton and nextButton if exist
    for (var i = 0, length = properties.buttons.length; i < length; i++) {
      if (properties.buttons[i].action === "save") {
        self.saveButton = properties.buttons[i];
      } else if (properties.buttons[i].action === "next") {
        self.nextButton = properties.buttons[i];
      }
    }

    var navigation = self.getNavigation();

    if (typeof navigation !== "undefined") {
      //si tiene un viewConteiner sera porque es una forma dentro de container de edicion
      var view_container = self.getViewContainer();
      if (typeof view_container !== "undefined") {
        view_container.setNavigationButtons(self);
      } else {
        navigation.setNavigationButtons(self);
      }
    }

    // bind de touch events
    self.bindTouchEvents(self.getButtons());
  },

  refreshForm: function refreshForm(focus) {
    var self = this;
    var params = self.getParams();
    var defer = new $.Deferred();

    focus = focus || self.getFocus();

    var options = {
      idCase: self.params.idCase || "",
      idWorkitem: self.params.idWorkitem || "",
      idTask: self.params.idTask || "",
      focus: focus,
      selectedTabs: self.getSelectedTabs(),
      selectedSection: self.getSelectedSection(),
      xpathContext: self.properties.xpathContext,
      idPageCache: self.properties.idPageCache,
      isRefresh: true,
      action: self.getEndPointAction(),
      idStartScope: self.properties.idStartScope || "",
      navigation: params.navigation,
      view_container: params.view_container };


    if (self.isOfflineRefreshWithEdition) {
      options.isEdited = true;
      self.isOfflineRefreshWithEdition = false;
    }

    if (params.postRenderEdit || params.processButtons) {
      options.postRenderEdit = params.postRenderEdit;
      options.processButtons = params.processButtons;
      options.originalParams = params.originalParams || {};
      options.getButtons = params.getButtons;
    }

    // Notify the refresh event so the consumer takes the decision about what to do
    self.startLoading();

    $.when(self.triggerHandler("refresh", options)).done(function () {
      defer.resolve();
    }).fail(function () {
      defer.reject();
    }).always(function () {
      self.endLoading();
    });

    return defer.promise();
  },

  startLoading: function startLoading(delay) {
    bizagi.util.smartphone.startLoading();
  },

  endLoading: function endLoading() {
    bizagi.util.smartphone.stopLoading();
  },

  /**
      * Template method to implement in each device to customize the container's behaviour to add handlers
      */
  configureHandlers: function configureHandlers() {
    var self = this;
    var form = self.container;

    // Process complex-gateway
    self.configureComplexGateway();

    // Process buttons
    self.processButtons(form);

    // Check for dom included event
    self.bind("ondomincluded", function () {
      var $canvas = self.getValidationsContainer();

      // Creates validation controller after the form is ready
      self.validationController = new bizagi.command.controllers.validation(self, self.validations, $canvas);

      // Creates action controller after the form is ready
      self.actionController = new bizagi.command.controllers.action(self, self.actions);

      // Resolve this deferred to start executing DOM dependant plugins
      self.readyDeferred.resolve();
    });

    //Event to notify the native code the form will be showing a modal view
    self.bind("modalViewDidAppear", function () {
      self.params.navigation.modalViewDidAppear();
    });

    self.bind("closeModalValidationMessage", function () {
      if (self.validationController) {
        self.validationController.clearValidationMessages();
      }
    });

    //Event to notify the native code the form will be hiding a modal view
    self.bind("modalViewDidDisappear", function () {
      self.params.navigation.modalViewDidDisappear();
    });

    // Add network listener
    self.configureNetworkHandlers();
  },

  internalSubmitForm: function internalSubmitForm(buttonProperties) {
    var self = this;
    var actions = [];
    var defer = $.Deferred();
    var submitDataAction = null;
    var executeButtonAction = null;
    var performAction = null;
    var checkpointAction = null;
    var commitAction = null;
    var data = {};

    // Fill data hash | Collect data
    if (buttonProperties.submitData || buttonProperties.executeRule || buttonProperties.createnewcase)
    data = self.collectRenderValuesForSubmit();

    // Start asynchronous operation
    $.when(self.startLoading()).done(function () {
      $('html, body').animate({ scrollTop: $('.ui-bizagi-loading-icon') }, 'slow');
    });

    // Prepare actions
    // When the button has to execute any rule (MUST SUBMIT DATA BEFORE ALWAYS)
    if (buttonProperties.executeRule) {
      submitDataAction = self.dataService.submitData({
        action: "SUBMITDATA",
        data: data,
        xpathContext: self.properties.xpathContext,
        idPageCache: self.properties.idPageCache,
        prepare: true });


      submitDataAction.tag = "submitData";
      checkpointAction = self.prepareCheckpointAction(buttonProperties.id);

      // Reset data because when an execute rule operation has been performed, it saved already all the data
      data = {};
      executeButtonAction = self.dataService.executeButton({
        idRender: buttonProperties.id,
        xpathContext: self.properties.xpathContext || "",
        idPageCache: self.properties.idPageCache,
        prepare: true });


      executeButtonAction.tag = "executeRule";
      commitAction = self.prepareCommitAction(buttonProperties.id);
    }

    if (buttonProperties.next || buttonProperties.back || buttonProperties.save || buttonProperties.createnewcase || buttonProperties.nextWithoutValidations) {
      // Verify transitions
      if (self.transitions) {
        var transitions = [];
        $.each(this.activeTransitions, function (key, value) {
          value ? transitions.push(key) : "";
        });

        if (bizagi.util.countProps(transitions) == 0) {
          var messageComplexGateway = bizagi.localization.getResource("render-widget-complexgateway-path");
          var errorComplexGateway = bizagi.localization.getResource("render-widget-complexgateway-error");

          bizagi.showMessageBox(messageComplexGateway, errorComplexGateway, "info", false);
          self.endLoading();
          defer.reject();
          return defer.promise();
        }
      }

      performAction = self.dataService.submitData({
        action: buttonProperties.action,
        forcePlanCompletion: buttonProperties.forcePlanCompletion,
        data: data,
        xpathContext: self.properties.xpathContext,
        idPageCache: self.properties.idPageCache,
        prepare: true,
        transitions: transitions || "",
        contexttype: self.properties.contextType,
        surrogatekey: self.properties.surrogateKey });


      performAction.tag = "performAction";
    }

    if (executeButtonAction) {
      actions.push(submitDataAction);

      //Validates if the method CHECKPOINT is supported
      if (checkpointAction) {
        actions.push(checkpointAction);
      }

      actions.push(executeButtonAction);

      //Validates if the method COMMIT is supported
      if (commitAction) {
        actions.push(commitAction);
      }
    }

    if (performAction)
    actions.push(performAction);

    //Evaluate if there are deferreds to evaluate, like inline add in grid, that must send a ADDRELATIONWITHDATA before to savedata
    var flowControlDeferred = new $.Deferred();

    if (data && data.deferreds) {
      $.when.apply(this, data.deferreds).
      done(function (response) {
        flowControlDeferred.resolve(response);
      }).fail(function () {
        console.log("Error: It was not possible resolve async actions on form ");
        flowControlDeferred.reject();
      });
    } else {
      flowControlDeferred.resolve();
    }

    $.when(flowControlDeferred).done(function () {
      bizagi.util.smartphone.startLoading();
      $.when(self.dataService.multiaction().execute({ actions: actions })).done(function (responses) {
        //Hard refresh buttons
        if (bizagi.util.isNativePluginSupported() && bizagi.util.isiOSSmartphoneDevice()) {
          var navigation = self.getNavigation();
          if (typeof navigation !== "undefined") {
            navigation.setNavigationButtons(self);
          }
        }
        // End asynchronous operation
        self.endLoading();
        // Find validations
        var bHasValidations;
        $.each(responses, function (i, response) {
          // Something went wrong, we gotta display messages in notifications grid
          if (response.result.type == "validationMessages") {
            self.addValidationMessage(response.result.messages);
            bHasValidations = true;
          }
          if (response.result.type == "alertMessages") {
            // Something went wrong, we are gonna display messages in an alert
            bizagi.showMessageBox(response.result.messages.join(','), bizagi.localization.getResource("workportal-general-word-information", "Information"));
            bHasValidations = true;
          }
        });

        if (!bHasValidations) {
          // Everything went ok .. succesfully response from the server
          if (!self.postProcessButton(responses, buttonProperties))
          return;

          // After successfully response refresh the form
          if (buttonProperties.refresh)
          self.refreshForm();

          // If needs routing publish an event
          if (buttonProperties.routing) {
            self.triggerHandler("routing", { idCase: self.params.idCase });
          }

          // Create case
          if (buttonProperties.createnewcase) {
            if (responses.length > 0) {
              self.triggerHandler("routing", {
                idCase: responses[0].result.IdCase,
                caseNumber: responses[0].result.CaseNumber });

            }
          }
        } else {
          if (buttonProperties.executeRule)
          self.sendRollbackAction(buttonProperties.id);
        }

        //Removes the nonSupportedMethods to prevent further iterations
        delete self.nonSupportedMethods;

        defer.resolve();
      }).fail(function (res, res2, response) {
        var messageError = "";
        var errorTypeList = ["WaitForCompletionPlanException", "InvalidPlanStateException", "CustomMessageException"];

        if (buttonProperties.executeRule) self.sendRollbackAction(buttonProperties.id);

        if (response && response.error && errorTypeList.indexOf(response.error.errorType) !== -1) {
          messageError = bizagi.localization.getResource(response.error.message);
          self.endLoading();

          $.when(bizagi.showConfirmationBox(messageError, null, false)).then(function () {
            var _params = $.extend({}, buttonProperties, { forcePlanCompletion: true });

            return $.when(self.internalSubmitForm(_params));
          }).done(function () {
            defer.resolve();
          }).fail(function () {
            defer.reject();
          });
        } else {
          messageError = typeof response === "undefined" ? bizagi.localization.getResource("workportal-general-error") : response;
          self.failHandler({ "error": messageError });
          defer.reject(response);
        }
      }).always(function () {
        bizagi.util.smartphone.stopLoading();
      });
    }).always(function () {
      bizagi.util.smartphone.stopLoading();
    });

    return defer.promise();
  },

  prepareCheckpointAction: function prepareCheckpointAction(id) {
    return bizagi.util.validateFeatureSupport("commitRollbackActions") ? this._super(id) : null;
  },

  prepareCommitAction: function prepareCommitAction(id) {
    return bizagi.util.validateFeatureSupport("commitRollbackActions") ? this._super(id) : null;
  },

  getNavigation: function getNavigation() {
    var params = this.getFormContainer().getParams();
    return params.navigation;
  },

  getViewContainer: function getViewContainer() {
    var params = this.getFormContainer().getParams();
    return params.view_container;
  },

  isFloatingButtonsEnabled: function isFloatingButtonsEnabled() {
    var navigation = this.getNavigation();
    return navigation && navigation.isFloatingButtonsEnabled();
  },

  isFormButtonsEnabled: function isFormButtonsEnabled() {
    var navigation = this.getNavigation();
    return navigation && navigation.isFormButtonsEnabled();
  },

  dispose: function dispose(params) {
    // Active refresh on smartphone
    if (typeof params === "undefined" || typeof params.isRefresh === "undefined" || params.isRefresh == false) {
      this._super();
    }
  },

  /**
      * Publish a global event that the facade will replicate, also will be triggered in the form
      */
  triggerGlobalHandler: function triggerGlobalHandler(eventType, data) {
    var self = this;

    if (!self.parent) {
      if (self.observableElement) {
        self.observableElement.trigger(eventType, data);
      }
    } else {
      self.parent.triggerGlobalHandler(eventType, data);
    }

    return self._super(eventType, data);
  },

  // TODO: REVIEW, ADD NOTIFIER PLUGIN!!!
  failHandler: function failHandler(error) {
    var self = this;
    self._super(error);

    var error_obj = {};

    try {
      error_obj = JSON.parse(error) || {};
    } catch (e) {
      error_obj = error.length && error.length > 1 ? error[0] : error;
    } finally {
      //This double validation has the purpose to get the real error object from two different levels ( error.error or error.error.error )
      if (error_obj.error) error_obj = error_obj.error;
      if (error_obj.error) error_obj = error_obj.error;

      // trigger the routing event in order to refresh the case
      if (typeof error_obj.code !== "undefined" && error_obj.code === "AUTH_ERROR") {
        self.refreshAfterSessionIdle();
      }
    }
  },

  /**
      * Display session IDLE message error and routing to process
      */
  refreshAfterSessionIdle: function refreshAfterSessionIdle() {
    // Show notification message
    this.showMessageSessionIdle();

    // Refresh the process
    this.routingToProcess();
  },

  /**
      * Show message for session idle error
      * @constructor
      */
  showMessageSessionIdle: function showMessageSessionIdle() {
    $.notifier.add({
      title: bizagi.localization.getResource("workportal-sessiontimeout-expired-title", "Your session has expired"),
      text: bizagi.localization.getResource("render-idle-expired-error", "Idle timer expired. For safety reasons this view will be refreshed automatically in 5 seconds. Note that any unsaved data is lost.").replace("%s", "5"),
      sticky: false,
      time: 5000 });

  },

  /**
      * Refresh the process
      */
  routingToProcess: function routingToProcess() {
    var self = this;
    var params = self.params || {};
    var form = self.getFormContainer();
    var isStartForm = form && form.isStartForm();

    setTimeout(function () {
      if (isStartForm) {
        var processParams = params && params.originalParams || {};
        if (processParams && processParams.idProcess) {
          bizagi.webpart.publish("render-start-form", {
            params: {
              showProcess: true,
              hasStartForm: true,
              fromNewCase: true,
              idProcess: processParams.idProcess,
              displayName: processParams.displayName || "",
              mapping: {},
              type: "Process" } });


        }
      } else {
        self.triggerHandler("routing", {
          idCase: params.idCase,
          idWorkitem: params.idWorkitem });

      }
    }, 5000);
  },

  /**
      * Build the container html
      */
  renderContainer: function renderContainer() {
    var self = this;
    var properties = this.properties;
    var template = self.renderFactory.getTemplate("form");
    var idCase = "";

    // Render the form
    var html = $.fasttmpl(template, {
      buttons: self.buttons,
      transitions: self.transitions,
      uniqueId: properties.uniqueId,
      helptext: properties.helpText,
      mode: self.getMode(),
      queryForm: self.getFormType() == "queryForm",
      summaryForm: self.params.summaryForm || false,
      globalForm: self.properties.displayAsReadOnly || false,
      idCase: idCase,
      displayName: self.params.displayName || idCase,
      processPath: properties.processPath,
      breadCrumb: properties.breadCrumb,
      orientation: properties.orientation });


    // Render children
    return self.replaceChildrenHtml(html, self.renderChildrenHtml());
  },

  bindTouchEvents: function bindTouchEvents(buttons) {
    var self = this;
    var touchMoveStart = 0;

    // Don't process buttons if they are undefined in the template
    if (!buttons || buttons.length === 0) return;

    buttons.bind("touchstart", function (event) {
      event.preventDefault();

      var pointer = event.originalEvent.targetTouches ? event.originalEvent.targetTouches[0] : event;
      var currentPossitionY = pointer.pageY;

      touchMoveStart = currentPossitionY;
    });

    buttons.bind("touchend", function (event) {
      event.preventDefault();

      // Trigger the blur of the focused element in order to catch the render Change in the last changed control
      $(":focus", self.container).blur();
      var pointer = event.originalEvent.changedTouches ? event.originalEvent.changedTouches[0] : event;
      var currentPositionY = pointer.pageY;

      self.startLoading();

      // If the finger is too far from the button when you stop touching the screen, then do nothing
      if (event.type !== "click" && currentPositionY > touchMoveStart + 40 || currentPositionY < touchMoveStart - 40) {
        self.endLoading();
      } else {
        var button = $(this);
        var ordinal = button.attr("ordinal");
        var buttonProperties = self.buttons[ordinal];

        // Execute button
        $.when(self.processButton(buttonProperties)).always(function () {
          self.endLoading();
        });
      }
    });

    buttons.bind("click", function () {
      self.startLoading();
      var ordinal = $(this).attr("ordinal");
      var buttonProperties = self.buttons[ordinal];

      // Execute button
      $.when(self.processButton(buttonProperties)).always(function () {
        self.endLoading();
      });
    });

    return;
  },

  /**
      * Configure actions to complex-gateway
      */
  configureComplexGateway: function configureComplexGateway() {
    var self = this;
    var complexGatewayContainer;
    var form = self.container;

    if (form.length > 1) {
      $(form.children()).each(function () {
        if ($(this).hasClass('.bz-complex-gateway'))
        complexGatewayContainer = this;
      });
    } else {
      complexGatewayContainer = $(".bz-complex-gateway", form);
    }

    // activeTransitions is used to store ids of activities in Complex Gateway
    self.activeTransitions = {};

    // Set events for complex gateway interface
    $(".bz-checkbox__input", complexGatewayContainer).on("click", function () {
      var $selected = $(this);
      var transitionId = $selected.data("value") || "";

      if (typeof self.activeTransitions[transitionId] == "undefined") {
        self.activeTransitions[transitionId] = false;
      }

      self.activeTransitions[transitionId] = $selected.is(':checked') || false;

    });
  },

  /**
      * Perform validation container
      * @param form
      * @param mainForm
      * @returns {*}
      */
  getValidationsContainer: function getValidationsContainer(mainForm) {
    var form = this;
    var navigation = form.getNavigation();

    if (typeof navigation !== "undefined") {
      var view_container = form.getViewContainer();
      var $currentView = typeof view_container !== "undefined" ? view_container.view : navigation.view;

      if ($currentView && $currentView.footer) {
        return $currentView.footer;
      }
    }

    return this._super(mainForm);
  } });