/**
 *   Name: Bizagi Smartphone ECM Slide Implementation
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will shows a ECM form
 */
$.Class.extend("bizagi.rendering.smartphone.slide.ecm", {}, {

  /**
                                                               * Constructor
                                                               * @param dataService
                                                               * @param renderFactory
                                                               * @param slideFormParams
                                                               */
  init: function init(dataService, renderFactory, slideFormParams) {
    var self = this;

    // Define instance variables
    self.dataService = dataService;
    self.renderFactory = renderFactory;

    self.slideFormParams = slideFormParams || {};
    self.slideFormDeferred = new $.Deferred();

    // Create container
    self.container = self.slideFormParams.navigation.createRenderContainer({ title: self.slideFormParams.title });
  },

  /**
      * Render the form
      * @param params
      */
  render: function render(params) {

    // Fill content
    this.renderSlideForm(params);

    // Return promise
    return this.slideFormDeferred.promise();
  },

  renderSlideForm: function renderSlideForm(params) {
    var self = this;
    var data = self.getMetadataForm(params);

    // Load template and data
    self.form = self.renderFactory.getContainer({
      type: "form",
      data: data.form,
      navigation: self.slideFormParams.navigation,
      view_container: self.container });


    // Binds the view close event
    /*self.container.element.bind("close", function () {
        // delete self.container;
        console.log("closed!");
    });*/

    $.when(self.form.render()).
    done(function ($renderEdition) {

      // Append form in the slide view
      self.container.element.html($renderEdition);

      self.slideFormParams.navigation.navigate(self.container.id);

      self.processButtons(self.form, params);
      self.container.setNavigationButtons(self.form);

      // Publish an event to check if the form has been set in the DOM
      self.form.triggerHandler("ondomincluded");

      // Attach a refresh handler to recreate all the form
      /*self.form.bind("refresh", function () {
          // self.renderSlideForm(params);
      });*/

    }).always(function () {
      self.slideFormDeferred.resolve();
    });

  },

  /**
      * Shows the slideForm form container in the browser
      * @returns {} Returns a promise that the dialog will be closed
      */
  processButtons: function processButtons(form, params) {
    var self = this;

    form.buttons = [
    {
      ordinal: 0,
      action: "save",
      caption: bizagi.localization.getResource("render-form-dialog-box-save"),
      callback: self.onSaveDataMetadataForm.bind(self, params.fileData) },

    {
      ordinal: 1,
      action: "back",
      caption: bizagi.localization.getResource("render-form-dialog-box-close"),
      callback: function callback() {
        self.slideFormParams.onCancel({});

        self.goBack();
      } }];



    // Add save button if the form is editable
    if (!self.slideFormParams.editable) {
      form.buttons.shift();
    }
  },

  /**
      * Process render data
      * @param params
      * @returns {{form: *, type: string}}
      */
  getMetadataForm: function getMetadataForm(params) {
    var self = this;
    var data = {
      type: "form",
      form: {
        elements: [],
        pageCacheId: params.pageCacheId,
        sessionId: params.sessionId,
        properties: {
          id: 101,
          xpathContext: params.xpathContext,
          editable: self.slideFormParams.editable,
          orientation: self.slideFormParams.orientation || "ltr" } } };




    // Mapping metadata to render
    $.each(params.metadata, function (index, item) {
      var renderType = self.getRenderType(item.metadataType);

      var newRender = Object.assign({
        "id": index + 1,
        "xpath": item.metadataId,
        "displayName": item.description,
        "required": bizagi.util.parseBoolean(item.required) === true,
        "editable": bizagi.util.parseBoolean(item.readOnly) === false,
        "value": item.value },
      renderType);

      data.form.elements.push({ "render": { "properties": newRender } });
    });

    return data;
  },

  /**
      * Get type of render
      * @param type
      * @returns {*}
      */
  getRenderType: function getRenderType(type) {
    switch (type.toUpperCase()) {
      case "INTEGER":
        return {
          type: "number",
          dataType: 2,
          allowDecimals: false,
          thousands: false };

      case "DECIMAL":
        return {
          type: "number",
          dataType: 10,
          allowDecimals: true,
          thousands: true };

      case "DATETIME":
        return { type: "date", dataType: 12 };
      case "BOOL":
        return { type: "boolean", dataType: 5 };
      case "STRING":
      default:
        return {
          type: "text",
          dataType: 15,
          maxLength: 50,
          autoExtend: false };}



  },

  /**
      * Save data to ECM form
      * @returns {jQuery}
      * @param fileData
      */
  onSaveDataMetadataForm: function onSaveDataMetadataForm(fileData) {
    var self = this;
    var deferred = $.Deferred();

    if (self.form.validateForm()) {
      // Check if the add form has data
      var formData = {};
      self.form.collectRenderValuesForSubmit(formData);

      // Turn off flag
      $.forceCollectData = false;

      if (!bizagi.util.isMapEmpty(formData)) {

        var metadataValues = Object.keys(formData).
        map(function (key, index) {
          return { "idMetadata": key, "value": formData[key] };
        });

        var params = {
          "idFileUpload": fileData.idFileUpload,
          "fileName": fileData.fileName,
          "idPageCache": fileData.idPageCache,
          "xpathContext": fileData.xpathContext,
          "xPath": fileData.xpath,
          "idAttrib": fileData.idAttrib };


        params.metaValues = JSON.encode({ metadataValues: metadataValues });
        bizagi.loading.start();

        $.when(self.dataService.updateECMMetadata(params)).
        done(function (fileProperties) {
          if (fileProperties && fileProperties.idFileUpload) {

            self.slideFormParams.onSave({
              update: true,
              idFileUpload: fileProperties.idFileUpload,
              fileName: fileData.fileName });


            deferred.resolve({ update: true });
          }

          self.goBack();

        }).fail(function (message) {
          self.form.addValidationMessage(message.message);
          deferred.reject();
        }).always(function () {
          bizagi.loading.stop();
        });
      } else {
        self.slideFormParams.onSave({ update: false });
        deferred.resolve({ update: false });
        self.goBack();
      }
    }

    deferred.promise();
  },

  /**
      * Go back and destroy
      */
  goBack: function goBack() {
    this.container.destroy();
  } });