/**
 *   Name: Bizagi Smartphone Render Money Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the money render class to adjust to smartphone devices
 */

// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.money", bizagi.rendering.number.prototype);
// Extends itself
bizagi.rendering.smartphone.money.extend("bizagi.rendering.smartphone.money", bizagi.rendering.money.prototype);
// Extends itself
bizagi.rendering.smartphone.money.extend("bizagi.rendering.smartphone.money", {}, {

  /**
                                                                                     * Method to customize each render after processed in read-only mode
                                                                                     * @returns {*|jQuery}
                                                                                     */
  renderReadOnly: function renderReadOnly() {
    var self = this;
    var template = self.renderFactory.getTemplate("number");

    return $.fasttmpl(template, {
      value: self.getDisplayValue(),
      editable: self.properties.editable,
      type: "text",
      isDesign: self.getMode() !== "execution" });

  },

  /**
      * Sets the value in the rendered control
      * @param {} value
      * @returns {}
      */
  setDisplayValue: function setDisplayValue(value) {
    // Call base
    this.setValue(value, false);

    // Set value in input
    if (value !== undefined && value !== null && this.properties.editable) {
      this.numericInput.val(value);

      // Formats the input
      this.executeFormatCurrencyPlugin();
    }
  },

  /**
      * Context to apply styles
      * @param context
      * @returns {jQuery|HTMLElement}
      */
  /*getElementsToStylize: function (context) {
             return $(".bz-input", context);
         }*/


  getElementsToStylize: function getElementsToStylize($context) {
    return $("input", $context);
  } });