/**
 *   Name: Bizagi Smartphone Render Template Money Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the money render class inside templates to adjust to smartphone devices
 */

bizagi.rendering.smartphone.layoutRender.extend("bizagi.rendering.smartphone.layoutMoney", bizagi.rendering.layoutNumber.prototype);
// Extends itself
bizagi.rendering.smartphone.layoutMoney.extend("bizagi.rendering.smartphone.layoutMoney", bizagi.rendering.layoutMoney.prototype);
bizagi.rendering.smartphone.layoutMoney.extend("bizagi.rendering.smartphone.layoutMoney", {
  /**
                                                                                             * Sets the value in the rendered control
                                                                                             */
  setValue: function setValue(val, triggerEvents) {
    var self = this;
    var control = self.getControl();

    val = typeof val === "undefined" || val === null ? "" : self.getDisplayValue(val);
    self.properties.displayName = val;

    this._super(val, triggerEvents);
    if (control) {
      $(".ui-bizagi-render-number", control).text(val);
    }
  } });