/*
*   Name: Bizagi Smartphone Panel Extension
*   Author: UI Forms Team
*   Comments:
*   -   This script will redefine the container class to adjust to smartphone devices
*/

// Extends from container smartphone base
bizagi.rendering.smartphone.container.extend("bizagi.rendering.smartphone.tabItem", bizagi.rendering.tabItem.prototype);
// Extends itself
bizagi.rendering.smartphone.tabItem.extend("bizagi.rendering.smartphone.tabItem", {}, {

  /**
                                                                                         * Change selected item
                                                                                         *
                                                                                         * @param {} argument
                                                                                         * @returns {}
                                                                                         */
  setAsActiveContainer: function setAsActiveContainer(argument) {
    var self = this;

    $.when(self.ready()).done(function () {
      var tabItem = $("[data-reference='#ui-bizagi-tab-" + self.properties.id + "']", self.parent.tabs);

      tabItem.css("display", "");
      tabItem.click();
    });
  },
  /* 
     *   Focus on container
     */
  focus: function focus() {
    // Activate the tab if it hasn't been loaded
    this.activate();

    // Set this tab as an active container
    this.setAsActiveContainer();

    // Call base
    this._super();
  },

  /**
      *  Hides / Show container
      *
      * @param {} argument
      * @returns {}
      */
  changeVisibility: function changeVisibility(argument) {
    var self = this;

    // Store in properties
    self.properties.visible = bizagi.util.parseBoolean(argument);

    $.when(self.ready()).done(function () {
      var tabItem = $("[data-reference='#ui-bizagi-tab-" + self.properties.id + "']", self.parent.tabs);
      var activeTab = self.parent.container.material_bizagi_tabs("getActiveTab");

      // Hide - show the render
      if (self.properties.visible) {
        tabItem.css("display", "");
        if (self.properties.counter === activeTab.index) {
          self.container.css("display", "");
          self.setAsActiveContainer();
        } else {
          self.container.css("display", "none");
        }
      } else {
        self.container.css("display", "none");
        tabItem.css("display", "none");

        // If we are focus in the same tab is going to be invisible
        activeTab = self.parent.container.material_bizagi_tabs("getActiveTab");

        if (self.properties.counter === activeTab.index) {
          self.parent.container.material_bizagi_tabs("moveToNextTab");
        } else {
          self.parent.container.material_bizagi_tabs("refreshPositions");
        }
      }
    });
  },

  afterToRefresh: function afterToRefresh() {
    var self = this;
    self._super();
    $.when(self.ready()).done(function () {
      self.parent.container.material_bizagi_tabs("refreshTab", self.properties.id);
    });
  } });