/**
 *   Name: Bizagi Smartphone Render list Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the list render class to adjust to smartphone devices
 */

// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.checkList", bizagi.rendering.checkList.prototype);
// Extends itself
bizagi.rendering.smartphone.checkList.extend("bizagi.rendering.smartphone.checkList", {}, {

  /**
                                                                                             *   Template method to implement in each device to customize each render after processed
                                                                                             */
  postRender: function postRender() {
    this._super();
    this.properties.unique = Math.ceil(Math.random() * 1000);
    this.properties.required = true;
  },

  /**
      *   Template method to implement in each device to customize the render's behaviour to add handlers
      */
  configureHandlers: function configureHandlers() {
    var self = this;
    // Call base
    self._super();
    $("input[type=checkbox]", self.getControl()).bind("change", function () {
      var itemId = $(this).data("item-guid");
      self.onCheckListGroupChange(itemId, this.checked);
    });
  },

  onCheckListGroupChange: function onCheckListGroupChange(itemId, checked) {
    var self = this;
    var item;
    var items = $.grep(self.properties.items, function (element, index) {
      return element.guid === itemId;
    });
    if (items && items.length > 0) {
      item = items[0];
    }
    item.checked = checked;
  },

  /**
      *   Sets the value in the rendered control
      */
  clearDisplayValue: function clearDisplayValue() {
    $("input[type=checkbox]", this.getControl()).removeAttr("checked");
  },

  setDisplayValue: function setDisplayValue(value) {
    var self = this;
  } });