/*
*   Name: Bizagi Smartphone Column Decorator Extension
*   Author: Bizagi UI Forms
*   Comments:
*   -   This script will redefine the column decorator class to adjust to smartphone devices
*/


// Extends itself
bizagi.rendering.columns.column.extend("bizagi.rendering.smartphone.columns.column", {}, {

  /*
                                                                                           *   Apply custom overrides to each decorated instance
                                                                                           */
  applyOverrides: function applyOverrides(decorated) {
    // Hacks the getControl method in the decorated render to add features
    if (decorated && decorated.getControl) {
      this.originalGetControl = decorated.getControl;
      decorated.getControl = this.getControl;
      decorated.properties.displayType = "normal";
    }
  },

  getContainerMessage: function getContainerMessage(surrogateKey) {
    var decorated = this.getDecorated(surrogateKey);
    return $(".bz-form-cell-message__text", decorated.element);
  },

  /*
     *   Template method to get the control element
     *   This code runs under the decorated element context
     */
  getControl: function getControl() {
    return $(".bz-control", this.element || this.observableElement);
  },

  isValueValid: function isValueValid(surrogateKey, value, messages, propertyOverrides) {
    var isValid = this._super(surrogateKey, value, messages, propertyOverrides);
    var decorated = this.getDecorated(surrogateKey);
    var $message = this.getContainerMessage(surrogateKey);

    if (decorated && decorated.element) {
      if (!isValid) {
        var message = messages.join(",").replace(/<\w+>([^<]*)<\/\w+>/g, "$1");
        $message.text(message);
        decorated.element.addClass("bz-form-item--error");
      } else {
        $message.text("");
        decorated.element.removeClass("bz-form-item--error");
      }
    }

    return isValid;
  },

  /*
     *   Apply column styles to a specified element
     */
  applyColumnStyles: function applyColumnStyles(element) {
    var self = this;
    var properties = self.properties;

    // Set cell back color
    if (properties.textFormat.background) {
      element.css("background-color", properties.textFormat.background);
      element.css("background-image", "none");
    }
  },

  /*
     *   Post process the element after it has been rendered
     */
  postRenderReadOnly: function postRenderReadOnly(surrogateKey, cell) {
    var self = this;

    // Call base        
    self._super(surrogateKey, cell);

    // Just draw render if the cell layout has been filled
    if (cell && cell.length > 0) {
      var cellIcon = $(".ui-bizagi-cell-icon", cell);
      if (!self.properties.editable) cellIcon.detach();

      // Apply styles to cell
      self.applyColumnStyles(cell);

      if (self.properties.align == "left") {
        cellIcon.addClass("ui-bizagi-cell-icon-right");
      }
    }
  } });