/**
 *   Name: Bizagi Smartphone Render Document Generator Extension
 *   Author: Bizagi UI Forms
 *   Comments:
 *   -   This script will redefine the document generator render class to adjust to smartphone devices
 */

// Extends itself
bizagi.rendering.smartphone.columns.column.extend("bizagi.rendering.smartphone.columns.document", {}, {

  /**
                                                                                                         * Apply custom overrides to each decorated instance
                                                                                                         * @param decorated
                                                                                                         */
  applyOverrides: function applyOverrides(decorated) {
    var self = this;
    var mode = self.getMode();

    self._super(decorated);

    if (mode !== "design") {
      self.surrogateKey = decorated.surrogateKey;

      decorated.getXpath = this.getXpath;
      decorated.getDocumentXpath = this.getDocumentXpath;
      decorated.getContextXpath = this.getContextXpath;
      decorated.documentTemplateGenerate = this.documentTemplateGenerate;
      decorated.preRenderControl = this.preRenderControl;
      decorated.deleteDocumentTemplate = this.deleteDocumentTemplate;
      decorated.properties.xpathContext = self.getContextXpath();
    }
  },

  /**
      * This method tries to get the HTML template an render the current object inside
      * @return {jQuery} $.Deferred()
      */
  renderControl: function renderControl() {
    this.preRenderControl();

    return this._super();
  },

  /**
      * Creates the event of generation of templates.
      */
  documentTemplateGenerate: function documentTemplateGenerate() {
    var self = this;
    var properties = self.properties;

    self.startLoading();

    // Get the context xPath
    properties.xpathcontext = self.getContextXpath();
    properties.xpath = self.getDocumentXpath(properties.xpath);

    var grid = self.parent;
    var form = self.getFormContainer();

    $.when(form.submitData()).done(function () {
      $.when(self.dataService.generateDocumentTemplate(properties)).done(function (response) {
        $.when(grid.refresh()).done(function () {
          self.autoOpenDocuments();
        });

        self.endLoading();

      }).fail(function () {
        self.showErrorMessage();
        self.endLoading();
      });
    }).fail(function (message) {
      // Add error messages
      self.getFormContainer().addErrorMessage(message);

      self.endLoading();
    });
  },

  /**
      * Apply custom overrides to each decorated instance
      * @param xpath
      * @returns {string}
      */
  getXpath: function getXpath(xpath) {
    var xpathPath = xpath;
    var index = xpathPath.indexOf(this.grid.properties.xpath);

    if (index !== -1) {
      xpathPath = xpathPath.substr(index + this.grid.properties.xpath.length + 1);
    }

    return this.grid.properties.xpath + "[id=" + this.surrogateKey + "]." + xpathPath;
  },

  /**
      * Returns the xpath to be used
      * @param xpath
      * @returns {*|string}
      */
  getDocumentXpath: function getDocumentXpath(xpath) {
    var xpathPath = xpath;
    var index = xpathPath.indexOf(this.grid.properties.xpath);

    if (index !== -1) {
      xpathPath = xpathPath.substr(index + this.grid.properties.xpath.length + 1);
    }

    return xpathPath;
  },

  /**
      * Post process the element after it has been rendered
      * @param surrogateKey
      * @param cell
      * @param tableCell
      */
  postRenderReadOnly: function postRenderReadOnly(surrogateKey, cell, tableCell) {
    // Call base
    this._super(surrogateKey, cell, tableCell);
  },

  /**
      * Returns the xpath to be used
      * @returns {string}
      */
  getContextXpath: function getContextXpath() {
    var self = this;
    var form = self.getFormContainer();
    var xpathContext = form.properties.xpathContext ? form.properties.xpathContext + "." : "";
    return xpathContext + this.grid.properties.xpath + "[id=" + this.surrogateKey + "]";
  },

  /**
      * Template method to implement in each children to customize each control
      */
  preRenderControl: function preRenderControl() {
    if (this.value != undefined && this.properties.value != undefined) {
      this.properties.value = this.value;
    }
  },

  /**
      * Creates the event of generation of templates.
      * @param filename
      */
  deleteDocumentTemplate: function deleteDocumentTemplate(filename) {
    var self = this;
    var grid = self.parent;
    var params = {};

    params.xpath = self.properties.xpath + "[fileName='" + filename + "']";
    params.idRender = self.properties.id;
    params.xpathContext = self.getContextXpath();
    params.idPageCache = self.properties.idPageCache;

    $.when(self.dataService.deleteUploadFile(params)).done(function () {
      grid.refresh();
    });
  } });