/**
 *   Name: Bizagi Render Image Class
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the image render class to adjust to smartphone devices
 */

// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.image", {
  // Statics
  BA_ACTION_PARAMETER_PREFIX: bizagi.render.services.service.BA_ACTION_PARAMETER_PREFIX,
  BA_CONTEXT_PARAMETER_PREFIX: bizagi.render.services.service.BA_CONTEXT_PARAMETER_PREFIX,
  BA_PAGE_CACHE: bizagi.render.services.service.BA_PAGE_CACHE },
bizagi.rendering.image.prototype);

// Extends itself
bizagi.rendering.smartphone.image.extend("bizagi.rendering.smartphone.image", {
  QUALITY_PICTURE: 80,
  EXTENSIONSIMG: ["jpeg", "jpg", "png"] },
{

  /**
   * DOM input to handle HTML uploads
   * @var $inputFile jQuery DOM
   */
  $inputFile: null,

  /**
                     * Whether or not is a native device 'Just a shortcut
                     * @var isNative boolean
                     */
  isNative: false,

  /**
                    * Whether or not we are offline 'Just a shortcut
                    * @var isOfflineForm boolean
                    */
  isOfflineForm: false,

  /**
                         * Update or init the element data
                         * @param {} data
                         */
  initializeData: function initializeData(data) {
    // Call base
    this._super(data);

    // Check if offline forms is supported
    this.isOfflineForm = bizagi.util.isOfflineForm({ context: this });
    this.isNative = bizagi.util.isCordovaSupported();

    if (this.isOfflineForm) {
      var maxFileSizeOfProperty = Number(this.properties.maxSize) || 1048576;
      var maxFileSizeOfUser = typeof BIZAGI_SETTINGS !== "undefined" && typeof BIZAGI_SETTINGS.UploadMaxFileSize !== "undefined" ? Number(BIZAGI_SETTINGS.UploadMaxFileSize) : 1048576;
      this.properties.maxSize = maxFileSizeOfProperty < maxFileSizeOfUser ? maxFileSizeOfProperty : maxFileSizeOfUser;
    }
  },

  /**
      * This method tries to get the HTML template an render the current object inside
      * @return {jQuery} $.Deferred()
      */
  renderControl: function renderControl() {
    var template = this.renderFactory.getTemplate("image");

    return $.fasttmpl(template, { url: "" });
  },

  getElementsToStylize: function getElementsToStylize(context) {
    return $({});
  },

  /**
      * There is no difference when rendering a readonly image, so...
      * @return {jQuery} $.Deferred()
      */
  renderReadOnly: function renderReadOnly() {
    var self = this;
    var template = this.renderFactory.getTemplate("image-readonly");

    return self.buildItemUrl().
    then(function (dataUrl) {
      var url = dataUrl || "";

      if (!self.isDisposed()) {
        self.properties.url = url;
        var html = $.fasttmpl(template, { url: self.properties.url });
        return html;
      }
    });
  },

  /**
      * After rendering element
      */
  postRender: function postRender() {
    var $control = this.getControl();
    this.$inputFile = $(".bz-image__input", $control);
    this.$nativeFile = $(".bz-image__native", $control);

    if (!this.isDesignMode())
    this.showAsyncImage();
  },

  /**
      * Add action handlers
      */
  configureHandlers: function configureHandlers() {
    var self = this;
    var $control = this.getControl();

    // Attach all events
    $control.on("click", "[data-action=upload]", function (evt) {
      self.choosePhoto();
    });

    if (this.isNative) {
      self.configureNativeHandlers();
    } else {
      self.configureHybridHandlers();
    }
  },

  configureNativeHandlers: function configureNativeHandlers() {
    var self = this;
    var $control = this.getControl();

    // Upload Image Options
    this.$nativeFile.off("click").actionSheet({
      actions: self.getUploadChoices(),
      actionClicked: function actionClicked(action) {
        switch (action.guid) {
          case 1:
            self.imageAdd();
            break;
          case 2:
            self.imageAdd(true);
            break;}

      },
      appear: function appear() {
        self.triggerGlobalHandler("closeModalValidationMessage", {});
        self.triggerGlobalHandler("modalViewDidAppear", {});
      },
      cancelClicked: function cancelClicked(action) {
        self.triggerGlobalHandler("modalViewDidDisappear", {});
      } });


    $(".bz-image__resolution", $control).off("click").actionSheet({
      actions: bizagi.util.media.getImageResolutionSizes(),
      withoutCancel: true,
      actionClicked: function actionClicked(action) {
        bizagi.loading.start();
        self.resolutionButtonHandler(action.guid).
        always(function () {
          bizagi.loading.stop();
        });
      },
      appear: function appear(action) {
        self.triggerGlobalHandler("closeModalValidationMessage", {});
        self.triggerGlobalHandler("modalViewDidAppear", {});
      },
      cancelClicked: function cancelClicked(action) {
        self.triggerGlobalHandler("modalViewDidDisappear", {});
      } });

  },

  configureHybridHandlers: function configureHybridHandlers() {
    var self = this;

    // Image change (Hybrid only)
    this.$inputFile.off("change").on("change", function () {
      self.imageAdd();
      this.value = "";
    });

    // Just in case
    this.$inputFile.off("click").on("click", function (evt) {
      evt.stopPropagation();
    });
  },

  /**
      * Template method to implement in each device to customize the render's behaviour
      * to add handlers when the control is readonly
      */
  configureHandlersReadOnly: function configureHandlersReadOnly() {
    var self = this;
    var $control = this.getControl();

    $control.on("click", ".bz-image", function () {
      self.imagePreview();
    });
  },

  /**
      * Add an image
      * @param useCamera boolean Whether or not to use device's camera
      */
  imageAdd: function imageAdd(useCamera) {
    var self = this;

    if (!this.isNative) {
      self.imageAddHybrid();
    } else {
      self.imageAddNative(useCamera);
    }
  },

  imageAddHybrid: function imageAddHybrid() {
    var self = this;
    var extensions = this.Class.EXTENSIONSIMG;
    var dataFile = this.$inputFile[0].files[0];

    dataFile.fullPath = this.$inputFile[0].value;
    bizagi.loading.start();

    if (bizagi.util.media.checkMaxSizeFile(dataFile, self.properties) && bizagi.util.media.checkFileTypes(dataFile, self.properties, extensions)) {
      self.performUploadFile(dataFile).
      always(function () {
        bizagi.loading.stop();
      });
    } else {
      bizagi.loading.stop();
    }
  },

  imageAddNative: function imageAddNative(useCamera) {
    var self = this;
    var options = {
      quality: self.Class.QUALITY_PICTURE,
      targetWidth: 1280,
      targetHeight: 960,
      correctOrientation: true };


    if (!useCamera) {
      var coordinates = self.getControl()[0].getBoundingClientRect();

      options = {
        quality: self.Class.QUALITY_PICTURE,
        sourceType: Camera.PictureSourceType.PHOTOLIBRARY,
        popoverOptions: new CameraPopoverOptions(coordinates.left, coordinates.top - 20, 200, 100, 4),
        destinationType: navigator.camera.DestinationType.FILE_URI,
        correctOrientation: true };

    }

    navigator.camera.getPicture(
    function (dataImage) {// on success
      return self.imageAddNativeFromCamera(dataImage);
    }, function () {// on fail 
      bizagi.loading.stop();
    }, options);

  },

  imageAddNativeFromCamera: function imageAddNativeFromCamera(dataImage) {
    var self = this;
    bizagi.loading.start();

    // Check max size, then get file data info and upload file
    bizagi.util.media.checkMaxSize(dataImage, self.properties).
    then(function () {
      return bizagi.util.media.getFileDataInfo(dataImage);
    }).
    then(function (fileData) {
      if (bizagi.util.media.checkFileTypes(fileData, self.properties, self.Class.EXTENSIONSIMG)) {

        self.dataImage = dataImage;
        self.fileProperties = fileData;

        // "new Blob" constructor is not supported in android version 4.2.2
        var version = bizagi.util.getAndroidVersion();
        if (version && version <= 4.2 || self.isOfflineForm) {
          self.performUploadFile(self.dataImage).
          always(function () {
            bizagi.loading.stop();
          });
        } else {

          if (!bizagi.util.isAndroid()) {
            return $.when(bizagi.util.media.modalResolutionUpload()).
            then(function (buttonIndex) {
              if (buttonIndex <= bizagi.util.media.getImageResolutionItems().length) {
                return self.resolutionButtonHandler(buttonIndex);
              }
            });
          } else {
            // Force click to show actionsheet menu
            $(".bz-image__resolution", self.getControl()).click();
          }
        }
      }
    }).
    always(function () {
      bizagi.loading.stop();
    });
  },

  /**
      * Remove the current image
      */
  imageDelete: function imageDelete() {
    var self = this;

    bizagi.loading.start();

    return self.dataService.deleteImage({
      url: self.properties.deleteUrl,
      idRender: self.properties.id,
      xpath: self.properties.xpath,
      xpathContext: self.properties.xpathContext,
      idPageCache: self.properties.idPageCache,
      contexttype: self.properties.contexttype }).
    then(function () {
      self.properties.value = [];
      self.uploadFileCompleted();
    }).fail(function (msg) {
      var message;
      var responseText = msg.responseText;

      try {
        message = JSON.parse(responseText);
        message = message.message || responseText;
      } catch (e) {
        message = responseText;
      }

      self.setValidationMessage(message);

    }).always(function () {
      bizagi.loading.stop();
    });
  },

  /**
      * Send file using HTTP
      * @param dataFile
      * @return {jQuery} $.Deferred()
      */
  uploadFile: function uploadFile(dataFile) {
    var self = this;
    self.properties.isChanged = true;

    if (!this.isOfflineForm) {
      if (this.isNative) {
        return self.uploadFileNativeOnline(dataFile);
      }
      return self.uploadFileHybridOnline(dataFile);
    }

    // Offline code
    if (bizagi.util.isEmpty(self.properties.value)) self.properties.value = [[null, null]];
    var fileName = self.fileProperties != undefined ? self.fileProperties.name : dataFile.name;
    self.dataService.fileService.changeOfflineImageName(self.properties.value, fileName);
    return self.saveImageOffline(dataFile);
  },

  uploadFileNativeOnline: function uploadFileNativeOnline() {
    var self = this;
    var params = {};
    var addParams = self.buildAddParams();
    var fileName = self.fileProperties.name;

    if (self.editedImageURL) {
      params.dataFile = self.editedImageURL;
    } else if (self.fileProperties) {
      params.dataFile = self.fileProperties.url;
    }

    addParams.queueID = "q_" + bizagi.util.encodeXpath(self.getUploadXpath());

    params.data = addParams;
    params.options = new FileUploadOptions();
    params.options.fileKey = "file";
    params.options.fileName = fileName;
    params.options.mimeType = "image/jpeg";
    params.options.uploadKey = { key: addParams["h_xpath"] };
    params.options.addUrl = self.properties.addUrl;

    //Cleans variables related previous uploads
    self.editedImageURL = null;
    self.fileProperties = null;

    // Fix android 4.4 getting images from recent folder
    params.dataFile = bizagi.util.media.getImagePath(params.dataFile);

    return $.when(bizagi.util.media.uploadFile(params)).
    then(function (result) {
      var response = JSON.parse(decodeURIComponent(result.response));
      if (response.type === "error") {
        return $.Deferred().reject(response).promise();
      }
      return response;
    });
  },

  uploadFileHybridOnline: function uploadFileHybridOnline(dataFile) {
    var self = this;

    if (bizagi.util.media.checkMaxSizeFile(dataFile, self.properties) &&
    bizagi.util.media.checkFileTypes(dataFile, self.properties, self.Class.EXTENSIONSIMG)) {

      return self.dataService.processUploadFile({
        url: self.properties.addUrl,
        formData: self.buildAddFileParams(dataFile) });


    } else {
      return $.Deferred().reject().promise();
    }
  },

  /**
      * File upload is done
      */
  uploadFileCompleted: function uploadFileCompleted() {
    try {
      this.showAsyncImage();
      this.editedImageURL = null;
    } catch (error) {
      // TODO: Review scenario
      this.getFormContainer().refreshForm();
    }
  },

  /**
      * Show async image
      * */
  showAsyncImage: function showAsyncImage() {
    var self = this;
    var $control = self.getControl();
    var $wrapper = $(".bz-input-icon", $control);

    self.renderUploadItem().
    then(function (htmlImage) {
      if (!self.isDisposed()) {

        // Empty container and add new image
        $wrapper.empty();
        $wrapper.append(htmlImage);

        var action = self.hasValue() ? "update" : "upload";

        $wrapper.attr("data-action", action);

        // Attack events to overflow-menu
        if (self.hasValue()) {
          self.configureMenu();
        }
      }
    });
  },

  /**
      * resolutionButtonHandler
      * @param ordinal
      */
  resolutionButtonHandler: function resolutionButtonHandler(ordinal) {
    var self = this;
    var actionSelected = ordinal || 0;

    // If original, save image normally, if not, reduce quality and size
    if (actionSelected === bizagi.util.media.ResolutionType.ORIGINAL) {
      return self.performUploadFile(self.dataImage);
    } else {

      var resolution = bizagi.util.media.getImageResolution(actionSelected);

      return $.when(bizagi.util.media.transformImageSize(self.dataImage, resolution)).
      then(function (result) {

        self.editedImageURL = result.localUrl;
        self.properties.url = result.base64Image;

        return self.performUploadFile(self.dataImage);
      });
    }
  },

  /**
      * Render edition view
      * @returns {}
      */
  imagePreview: function imagePreview() {
    var self = this;
    var baseParams = self.getParams();
    var container = baseParams.navigation.createRenderContainer({
      title: self.properties.displayName,
      view: {
        zoom: true,
        useNativeScrolling: false } });



    var form = self.getFormContainer();
    var template = self.renderFactory.getTemplate("image-preview");
    var imageData = { "url": self.properties.url && self.properties.url !== "" ? self.properties.url : "" };
    var img = $.fasttmpl(template, imageData);

    container.element.css("display", "none");
    self.inputEdition = container.element.html(img);
    baseParams.navigation.navigate(container.id);

    bizagi.loading.start();

    var getFullImage = form.properties.compatibilityVersion >= 2 ? function () {
      return imageData.url;
    } : self.buildFullItemUrl.bind(self);

    $.when(getFullImage()).then(function (dataUrl) {
      container.element.css("display", "block");

      if (dataUrl.indexOf("error") < 0) {
        var $preview = $(".bz-image__preview-item:first", container.element).attr("src", dataUrl);
        var originalImageWidth = $preview.css("width");

        originalImageWidth = Number(originalImageWidth.substring(0, originalImageWidth.length - 2));

        if (self.properties.width !== -1 && self.properties.width < originalImageWidth) {
          $preview.css("width", "100%");
        }
      }
    }).always(function () {
      bizagi.loading.stop();
    });
  },

  /**
      * Build add params to send to the server
      * @param {} dataFile
      * @returns {}
      */
  buildAddFileParams: function buildAddFileParams(dataFile) {
    var self = this;
    var properties = self.properties;
    var prefix = self.Class.BA_CONTEXT_PARAMETER_PREFIX;

    // Get form data for POSTing
    var formData = new FormData();
    var form = self.getFormContainer();

    // Build add params to send to the server
    formData.append(prefix + "xpath", properties.xpath);
    formData.append(prefix + "idRender", properties.id);
    formData.append(prefix + "xpathContext", properties.xpathContext);
    formData.append(prefix + self.Class.BA_PAGE_CACHE, properties.idPageCache);
    formData.append(self.Class.BA_ACTION_PARAMETER_PREFIX + "sessionId", form.properties.sessionId);
    formData.append(prefix + "action", "savefile");
    formData.append(self.properties.xpath, dataFile);

    if (properties.contexttype) {
      formData.append(prefix + "contexttype", properties.contexttype);
    }

    try {
      if (typeof BIZAGI_SESSION_NAME !== "undefined") {
        formData.append(BIZAGI_SESSION_NAME, form.properties.sessionId);
      } else {
        formData.append("JSESSIONID", form.properties.sessionId);
      }
    } catch (e) {
      formData.append("JSESSIONID", form.properties.sessionId);
    }

    return formData;
  },

  /**
      * Call the form failhandler with an specific error
      * @param {} error
      * @returns {}
      */
  executeFormFailHandler: function executeFormFailHandler(error) {
    var self = this;
    var errorResponse = error.responseText ? error.responseText : bizagi.localization.getResource("workportal-general-error");

    self.getFormContainer().failHandler(errorResponse);
  },

  /**
      * Execute upload file
      * @param {} context
      * @param {} dataImage
      * @returns {}
      */
  performUploadFile: function performUploadFile(dataImage) {
    var self = this;

    return $.when(self.uploadFile(dataImage)).
    then(function () {
      if (bizagi.util.isEmpty(self.properties.value)) self.properties.value = [[0, "uploaded"]];
      self.uploadFileCompleted();
    }).fail(function (error) {
      self.executeFormFailHandler(error);
    });
  },

  /**
      * Choose photo
      */
  choosePhoto: function choosePhoto() {
    if (this.isNative) {
      this.$nativeFile.click();
    } else if (bizagi.util.media.fileAPISupported() && !this.isOfflineForm) {
      this.$inputFile.click();
    }
  },

  /**
      * Process an action in order to do something with the image
      */
  configureMenu: function configureMenu() {
    var self = this;
    var items = [];
    var $control = this.getControl();
    var $viewMore = $(".bz-input-icon__input + .bz-icn", $control);
    var allowDelete = self.isOfflineForm ? false : self.properties.allowDelete && self.properties.editable;

    items.push({
      name: bizagi.localization.getResource("workportal-actionsheet-upload-image-preview"),
      fn: function fn() {
        self.imagePreview();
      } });


    items.push({
      name: bizagi.localization.getResource("workportal-actionsheet-upload-choose-photo"),
      fn: function fn() {
        self.choosePhoto();
      } });


    if (allowDelete) {
      items.push({
        name: bizagi.localization.getResource("workportal-actionsheet-upload-remove-photo"),
        fn: function fn() {
          bizagi.showConfirmationBox(self.getResource("render-grid-delete-confirmation")).
          then(function () {
            self.imageDelete();
          });
        } });

    }

    $viewMore.overflowMenu({ items: items });
  },

  /**
      * Add upload options
      */
  getUploadChoices: function getUploadChoices() {
    return [
    { "guid": 1, "displayName": bizagi.localization.getResource("workportal-actionsheet-upload-choose-photo") },
    { "guid": 2, "displayName": bizagi.localization.getResource("workportal-actionsheet-upload-take-photo") }];

  },

  /**
      * Builds the upload item url
      * @param params
      * @returns {*}
      */
  buildItemUrl: function buildItemUrl(params) {
    var self = this;

    if (self.isOfflineForm) {
      return self.buildItemUrlOffline();
    }

    return self.buildItemUrlOnline(params);
  },

  buildItemUrlOffline: function buildItemUrlOffline() {
    var self = this;
    return bizagi.util.sleepUntil(function () {
      return !self.dataService.getFileService().isLoading(self);
    }).then(function () {
      return bizagi.rendering.image.prototype.buildItemUrlOffline.apply(self, arguments);
    });
  },

  buildItemUrlOnline: function buildItemUrlOnline() {
    return bizagi.rendering.image.prototype.buildItemUrl.apply(this, arguments);
  },

  collectData: function collectData(renderValues) {
    var self = this;

    if (self.isOfflineForm && bizagi.util.isOfflineFormsV2Supported()) {
      if (!self.properties.isChanged) return;

      var xpath = self.properties.xpath;
      if (!bizagi.util.isEmpty(xpath)) {
        renderValues[xpath] = {
          offlineId: self.buildOfflineImageId(),
          name: self.dataService.fileService.getOfflineImageName(self.getValue()) };

      }
    }
  },

  /**
      * Cleans current value
      */
  cleanData: function cleanData() {
    if (!this.hasValue()) return;
    return $.when(this.imageDelete());
  } });